import { isFunction, isUndefined } from 'underscore';
module.exports = () => {
	let em;
  var c = {},
    commands = {},
    settingsCommand = {},
    defaults = require('./config/config');
  const active = {};
  return {
    /**
     * Name of the module
     * @type {String}
     * @private
     */
    name: defaults.label,

    /**
     * Initialize module
     */
    init(config) {
    	c = config || {};
      for (var name in defaults) {
          if (!(name in c)) c[name] = defaults[name];
      }
      em = c.em;
      var ppfx = c.pStylePrefix;
      if (ppfx) c.stylePrefix = ppfx + c.stylePrefix;
      let editor = em.get('Editor');

      settingsCommand = require('./view/settingsView');
      editor.Commands.add('open-settings', settingsCommand);
      editor.SettingManager = this;
      let googlfonts = JSON.parse(em.get('Editor').Config.settingsManager.loadgooglefont);
      this.fontdefaults = googlfonts;
      em.get('Editor').Panels.addButton('views', {
        id: 'open-settings',
        label: '<span>Settings</span>',
        className: 'far fa-cogs jpb-settings',
        command: 'open-settings',
        attributes: {
            title: '',
            'data-tooltip-pos': 'right',
            'data-tooltip': 'Settings Manager'
        },
        active: false,
      });
      editor.on('load', () => {
      	em.get('Editor').SettingManager.loadhead();
      })
      return this;
    },
    loadhead(){
    	let googlfonts = this.fontdefaults;
      	let fontArr = Object.values(googlfonts),fontname = [];
      	fontArr.forEach(function(font){
	      	let fontval = font.name;
	      	if(font.weight.length) fontval += ":"+font.weight.join(',');
	      	fontname.push(fontval);
	      });
	      //add head load google fonts 
	      const $ = em.get('Editor').Canvas.getWindow().jQuery;
	      if(fontname){
    			$('head').find('#linkloadfonts').remove();
    			$('<link id="linkloadfonts" href="https://fonts.googleapis.com/css?family='+escape(fontname.join('|'))+'" rel="stylesheet" type="text/css" />').appendTo('head');
    		}
    },

    loadFontWeight(sm,list) {
    	const weightProperty = this.getProps('font-weight');
  		weightProperty.set('list', list);
			weightProperty.view.updateOptions();
    },
    getProps(){
      let sectors = em.get('Editor').StyleManager.getSectors().models;
      let weightProp = '';
      sectors.forEach(sec => {
        let name = sec.get('id');
        let props = em.get('Editor').StyleManager.getProperty(name, 'font-weight');
        if(typeof props.id !== 'undefined') weightProp = props;
      });
      return weightProp;
    },
    setValue(data){
    	this.fontdefaults = JSON.parse(data);
    	this.loadhead();
    	return this;
    },
  };
    

};
