export default (editor, options = {}) => {
    var loadJSON = function(path, success, error) {
            var xhr = new XMLHttpRequest();
            xhr.onreadystatechange = function() {
                if (xhr.readyState === XMLHttpRequest.DONE) {
                    if (xhr.status === 200) {
                        if (success)
                            success(JSON.parse(xhr.responseText));
                    } else {
                        if (error)
                            error(xhr);
                    }
                }
            };
            xhr.open("GET", path, false);
            xhr.send();
        }
        // load t4 blocks/components/joomla data
    let blockurl = (options.blockUrl || 'dist/blocks.json');
    blockurl += (blockurl.match(/\?/) ? '&' : '?') + 't=' + Math.random();

    loadJSON(blockurl, data => {
        data = {
            assets: {},
            blocks: [],
            modules: [],
            menu: [],
            positions: ['pos-1', 'pos-2', 'sidebar'],
            modulestyles: ['raw', 'xhtml', 'rounded', 'table', 'card'],
            ...data
        };
        // store data into editor
        editor.editor.set('t4-config', data);

        // callback
        // callback();
    }, xhr => {
        throw new Error("Bad response from server");
    });

}
