export default (editor, config = {}) => {
    const em = editor.editor;
    const dc = editor.DomComponents;

    //set smgroup video component
    const videoModel = dc.getType("video").model;
    videoModel.prototype.defaults = {
        ...videoModel.prototype.defaults,
        'smgroup':'accordion',
    };
    //set smgroup map component
    const mapModel = dc.getType("map").model;
    mapModel.prototype.defaults = {
        ...mapModel.prototype.defaults,
        'smgroup':'accordion',
    };
    //set smgroup map component
   /* const textModel = dc.getType("text").model;
    textModel.prototype.defaults = {
        ...textModel.prototype.defaults,
        'smgroup':'box'
    };*/
    //set smgroup image component
    const imageModel = dc.getType("image").model;
    imageModel.prototype.defaults = {
        ...imageModel.prototype.defaults,
        'smgroup':'accordion',
        unstylable: [
          "font-family",
          "font-size",
          "font-weight",
          "letter-spacing",
          "color",
          "line-height",
          "text-align",
          "text-decoration",
          "text-shadow",
          "padding",
        ],
    };
    //set smgroup link component
    const linkModel = dc.getType("link").model;
    linkModel.prototype.defaults = {
        ...linkModel.prototype.defaults,
        'smgroup':'text',
        unstylable: [
            "background",
            "background-image",
        ],
    };

    //
};
