module.exports = () => {

    return {
        name: 'HelpManager',
        helpurl: 'https://static.joomlart.com/',
        tips: null,
        /**
         * Initialize module
         */
        init(opts = {}) {
            this.em = opts.em;
            if (opts.helpurl) this.helpurl = opts.helpurl;

            opts.em.view.$el.on('click', '.t4-helper', e => {
                var helpkey = e.target.getAttribute('key');
                if (helpkey) {
                    this.showTip(helpkey);
                }
                return false;
            })
            return this;
        },

        showTip(key) {
            if (!this.tips) {
                fetch(this.helpurl).then(res => {
                    return res.json();
                }).then(json => {
                    this.tips = json.data;
                    this.showTip(key);
                })
            } else {
                const modal = this.em.getEditor().Modal;
                var helpcontent = `
                <div class="t4-tip">
                  ${this.tips[key]}
                </div>
                `;
                var mdlDialog = document.querySelector('.gjs-mdl-dialog');
                var currentCls = mdlDialog.className;
                mdlDialog.className += ' t4-tips-diablog';

                modal.setContent(helpcontent);
                modal.setTitle('T4 Help');
                modal.open();

                modal.getModel().once('change:open', function() {
                    mdlDialog.className = currentCls;
                })

            }
        }
    };
};
