export default {
    extend: 'link',
	model: {
		defaults: {
            name: "button",
            smgroup: 'text',
            unstylable: [
                "background",
                "background-image",
            ],
            traits: [
                {
                    type: 'select',
                    label: 'Tag',
                    name: 'tagName',
                    default: "button",
                    changeProp: 1,
                    options: ['button', 'a']
                }

            ],
            draggable: true,
            droppable: true,
            attributes: {
            	class: 'btn'
            },
		},
        init() {
            this.em.getEditor().DomComponents.getType('link').model.prototype.init.apply(this, arguments);
            if (this.opt.temporary) return;
            var traits = [];
            var tag = this.get('tagName');
              switch (tag) {
                    case 'a':
                      traits = this.getLinkTrait();
                      break;
                    case 'button':
                      traits = this.getButtonTrait();
                      break;
                    default:
                      this.set('tagName', 'button');
                  }
                  this.set('traits', traits);
            this.listenTo(this, 'change:tagName', this.updateTraits);
            // bind traits for class
            if(tag == 'button'){
                const classes = [];
                let tr = this.get('traits');
                tr.each(trait => {
                    if(trait.get('name') != 'tagName')
                        this.detectClass(trait);
                });
            }
        },
        updateTraits () {
            let tag = this.get('tagName');
            var traits = this.getButtonTrait();
          switch (tag) {
            case 'a':
              traits = this.getLinkTrait();
              break;
            case 'button':
              traits = this.getButtonTrait();
              break;
            default:
              this.set('tagName', 'button');
          }
          this.loadTraits(traits);
          this.em.trigger('component:toggled');
        },
        getTypeTagTrait(){
            return {
                    type: 'select',
                    label: 'Tag',
                    name: 'tagName',
                    default: "button",
                    changeProp: 1,
                    options: ['button', 'a']
                };
        },
        getLinkTrait(){
            return [
                "id",
                "title",
                this.getTypeTagTrait(),
               
                "href",
                "target"
            ];
        },
        getButtonTrait(){
            return [
                "id",
                "title",
                this.getTypeTagTrait(),

                {
                    label: "Button Type",
                    name: "btn-type",
                    type: "select",
                    changeProp: 1,
                    options: [{
                        name: "Default",
                        value: ""
                    }, {
                        name: "Primary",
                        value: "btn-primary"
                    }, {
                        name: "Secondary",
                        value: "btn-secondary"
                    }, {
                        name: "Dark",
                        value: "btn-dark"
                    }, {
                        name: "Light",
                        value: "btn-light"
                    }, {
                        name: "Info",
                        value: "btn-info"
                    }, {
                        name: "Success",
                        value: "btn-success"
                    }, {
                        name: "Warning",
                        value: "btn-warning"
                    }, {
                        name: "Danger",
                        value: "btn-danger"
                    }],
                }, {
                    label: "Outline Type",
                    name: "btn-outline",
                    type: "select",
                    changeProp: 1,
                    options: [{
                        name: "Default",
                         value: ""
                    }, {
                        name: "Primary",
                        value: "btn-outline-primary"
                    }, {
                        name: "Secondary",
                        value: "btn-outline-secondary"
                    }, {
                        name: "Dark",
                        value: "btn-outline-dark"
                    }, {
                        name: "Light",
                        value: "btn-outline-light"
                    }, {
                        name: "Info",
                        value: "btn-outline-info"
                    }, {
                        name: "Success",
                        value: "btn-outline-success"
                    }, {
                        name: "Warning",
                        value: "btn-outline-warning"
                    }, {
                        name: "Danger",
                        value: "btn-outline-danger"
                    }],
                }, {
                    label: "Button Size",
                    name: "btn-size",
                    type: "select",
                    changeProp: 1,
                    options: [{
                        name: "Default",
                         value: ""
                    }, {
                        name: "Large",
                        value: "btn-lg"
                    }, {
                        name: "Small",
                        value: "btn-sm"
                    }]
                }, {
                    label: "Button Display",
                    name: "btn-display",
                    type: "select",
                    changeProp: 1,
                    options: [{
                        name: "Inline",
                         value: "",
                    }, {
                        name: "Block",
                        value: "btn-block"
                    }]
                }
            ];
        },
        detectClass(trait) {
            const classes = trait.get('options').map(opt => typeof opt == 'string' ? opt : opt.value);
            const name = trait.get('name');
            this.getClasses().forEach(cls => {
                if (classes.indexOf(cls) >= 0) {
                    this.set(name, cls);
                }
            })
            this.on('change:' + name, () => {
                const clss = this.getClasses(),
                    prevCls = this.previous(name),
                    cls = this.get(name),
                    idx = clss.indexOf(prevCls);
                if(cls == ""){
                    clss.splice(idx,1);
                }
                else if (idx > -1) clss[idx] = cls;
                else clss.push(cls);
                this.setClass(clss);
            })
        },
	},
	isComponent: el => {
        if(!el.tagName) return false;
        return ((el.tagName == 'BUTTON' || el.tagName == 'A') && el.matches && el.matches('.btn'));
    }
}