import Base from './base';
import {updateAssets} from './helper/assets';

export default (editor, options = {}) => {

    const types = [
        'element',
        'control',
        'ghost',
        'container',
        'section',
        'alert',
        'accordion',
        'carousel',
        'tab',
        'row',
        'column',
        'link',
        'box',
    ];

    const dc = editor.DomComponents;

    dc.addType('default', Base);

    types.forEach(type => {
        const com = require('./' + type).default
        dc.addType('t4.' + type, {
            isComponent: el => {
                if (!el.getAttribute) return false;
                if (type == 'section' && el.getAttribute('t4-group') == 'Block') return true;
                if (type == 'element') {
                    if (el.getAttribute('t4-element')) {
                        // detect t4-bid
                        const bid = el.getAttribute('t4-element').replace(/\./g, '_');
                        const element = editor.editor.get('t4-config').blocks.find(bl => bl.id.indexOf(bid) !== -1);
                        if (element) {
                            return {type: 't4.' + type, bid: element.id}
                        }
                    }
                    if (el.getAttribute('t4-group') == 'Element') return true;
                    
                } 
                return false;
            },
            ...com
        });
    })

    // update assets for section
    const wrapper = editor.getWrapper();
    wrapper.listenTo(wrapper.components(), 'add remove', (model) => {
        if (model.opt.temporary) return;
        updateAssets(editor);
    })

    editor.on('load', () => {updateAssets(editor)});
}
