export default (editor, config = {}) => {

  const tm = editor.TraitManager;
  // Select trait that maps a class list to the select options.
  // The default select option is set if the input has a class, and class list is modified when select value changes.
  tm.addType('select_group', {
    events:{
      'change': 'onChange',  // trigger parent onChange method on input change
    },
    getInputEl: function() {

      if (!this.$input) {
        var md = this.model;
        var opts = md.get('options') || [];
        var input = document.createElement('select');
        input.innerHTML = "<option value=''>---Select---</option>";
        input.setAttribute("class",'selectgroup');
        for ( var optgr in opts) {
        let optsgroup = document.createElement('optgroup');

          for (var i = opts[optgr].length - 1; i >= 0; i--) {
            
            optsgroup.setAttribute("label", optgr);
            var type_opts = typeof opts[optgr][i];
            var name,value;
            switch (type_opts) {
              case 'string':
                name = value = opts[optgr][i];
                break;
              case 'object':
                name = opts[optgr][i].name;
                value = opts[optgr][i].value;
                if(!Object.values(opts[optgr][i]).some(x => (x.value !== null && x.value !== ''))){
                  value = name;
                }
                break;
              case 'array':
                name = opts[optgr][i]['name'];
                value = opts[optgr][i]['value'];
                if(!opts[optgr][i].every(x => (x.value !== null && x.value !== ''))){
                  value = name;
                }
                break;
            }
            let option = document.createElement('option');
            option.text = name;
            option.value = value;
            optsgroup.append(option);
          }
          input.append(optsgroup);
        }

        this.input = input;

        this.$input = $(this.input);
        this.$input.append('<div class="gjs-sel-arrow"><div class="gjs-d-s-arrow"></div></div>');
        var target = this.target;
        var name = md.get('name');
        var val = md.get('value');

        if (md.get('changeProp')) {
          val = val || target.get(name);
        } else {
          var attrs = target.get('attributes');
          val = attrs[name];
        }

        if (val) this.$input.val(val);
      }
      return this.input;
    },
    templateInput() {
      const { ppfx, clsField } = this;
      return `<div class="${clsField}">
        <div data-input></div>
        <div class="${ppfx}sel-arrow">
          <div class="${ppfx}d-s-arrow"></div>
        </div>
      </div>`;
    },
    
  });

}
