import _ from 'underscore';
import Backbone from 'backbone';

module.exports = Backbone.View.extend({
    template: _.template(`
        <label><%= label %></label>
        <div class="<%= ppfx %>field <%= ppfx %>crumbs">
          <div id="<%= ppfx %>input-holder">
          </div>
        </div>
    `),

    initialize(o) {
        this.config = o.config || {};
        this.em = this.config.em;
        this.ppfx = this.config.pStylePrefix || '';
        this.delegateEvents();
    },
    getCrumbs(){
        var result = '';
        const collect = this.defaultModel | [];
        if(collect.length){
            collect.each(crumb => {
                result += '<div class="' + crumb.get('name') + '">' + crumb.get('name') + '</div>';
            })
        }
        return result;
    },
      /**
       * Update select value on device update
       * @private
       */
      /**
       * Return styles options
       * @return {string} String of options
       * @private
       */

    render() {
        var pfx = this.ppfx;
        this.$el.html(
          this.template({
            ppfx: pfx,
            label: ''
          })
        );
        this.crumb = this.$el.find('#' + pfx + 'input-holder');
        this.crumb.append(this.getCrumbs());
        this.el.className = pfx + 'crumbs-view-c';
        return this;
    }
});
