export default (editor, options = {}) => {
    var siteUrl = options.siteUrl;
    const bm = editor.BlockManager;
    const domc = editor.DomComponents;
    const defaultType = domc.getType('default');
    const defaultModel = defaultType.model,
        defaultView = defaultType.view;
    const comType = 'share-block';
    var data = {};
    

    domc.addType(comType, {
        model: defaultModel.extend({
            defaults: {
                ...defaultModel.prototype.defaults,
                tagName: 't4:block',
                droppable: false,
                stylable: false,
                attributes: {
                    name: ''
                }
            },
            toHTML() {
                return '<T4:Block name="' + this.getAttributes().name + '" ></T4:Block>';
            },
            init() {
                const tb = this.get('toolbar');
                tb.push ({
                    attributes: { 
                        class: 'far fa-edit',
                        title: 'Edit Share Blocks',
                        'data-tooltip': 'Edit Share Blocks',
                        'data-tooltip-pos': 'bottom'
                    },
                    command: e => this.enableEdit.apply(this)
                });
                data = this.getData();
            },
            getName() {
                return this.getAttributes().name;
            },
            getData(name = 0) {
                let result;
                const blocks = editor.editor.get('t4-config').blocks;
                const inishareblocks = blocks.filter(el => el.category == 'UserShareBlock');
                const initdata = {};
                if (inishareblocks.length) {
                    inishareblocks.forEach(share => {
                        initdata[share.name] = share.data_content;
                    })
                }
                if(name){
                    result = initdata[name];
                }else{
                    result = initdata;
                }
                return result;
            },
            getContent() {
                return data[this.getName()];
            },
            setContent(content) {
                data[this.getName()] = content;
            },
            enableEdit() {
                // disable autosave
                editor.StorageManager.getConfig().autosave = false;

                // switch content to components
                this.set('content', '<div class="shareblock-mask"></div><button class="shareblock-btn-update">Update</button><button class="shareblock-btn-cancel">Cancel</button>');
                const coms = this.components();
                coms.reset();
                coms.add(this.getContent());
                this.editMode = true;

                // remove toolbar
                this._tb = this.get('toolbar');
                this.set('toolbar', []);

                this.view.render();
                // reselected
                editor.DomComponents.selectAdd(this);
            },
            disableEdit() {
                this.editMode = false;

                // switch components to content
                this.set('content', this.getContent());
                this.components().reset();

                // restore toolbar
                if (this._tb) {
                    this.set('toolbar', this._tb);
                    delete this._tb;
                }

                this.view.render();
                // reselected
                editor.DomComponents.selectAdd(this);

                // enable autosave
                editor.StorageManager.getConfig().autosave = true;
            },
            saveEdit() {
                let content = '';
                // html content
                this.components().each(com => content += com.toHTML());
                // css
                let style = editor.CodeManager.getCode(this, 'css', { cssc: editor.CssComposer });
                // clean style
                style = style.replace(/(\*|body)\s*\{[^\}]*\}/gmi, '').trim();

                content += '\n<style>' + style + '</style>';
                this.setContent(content)

                // save to shareblock on server
                const name = this.getName();
                const category = 'UserShareBlock';
                fetch(siteUrl + (siteUrl.match(/\?/) ? '&' : '?') + em.get('builderParam') + '=saveblock&name=' + name + '&category=' + category, {
                    method: 'POST',
                    body: JSON.stringify({ content })
                })

                this.disableEdit();
            },
            cancelEdit() {
                this.disableEdit();
            }
        }, {
            isComponent(el) {
                if (el.tagName && el.tagName == 'T4:BLOCK') {
                    const name = el.getAttribute('name'),
                        content = data[name];
                    return {
                        type: comType,
                        content,
                        components: []
                    };
                }
            },
        }),
        view: defaultView.extend({
            events: {
                'click .shareblock-btn-update': "onUpdate",
                'click .shareblock-btn-cancel': "onCancel",
            },
            postRender() {
                if (this.model.editMode) {
                    if (!this.mask) this.enableEdit();
                    this.events = {
                        'click .shareblock-btn-update': "onUpdate",
                        'click .shareblock-btn-cancel': "onCancel",
                    }
                } else {
                    this.events = {};
                    this.disableEdit();
                }
                return;
            },

            enableEdit() {
                this.$el.removeClass('no-events')
                    .addClass('shareblock-edit')
            },

            disableEdit() {
                this.$el.addClass('no-events').removeClass('shareblock-edit');
            },

            onUpdate() { this.model.saveEdit(); return false; },
            onCancel() { this.model.cancelEdit(); return false; }
        }),
    });

}
