// import grapesjs from "grapesjs";
// import pluginPreset from 'grapesjs-preset-webpage';

import loadData from "./data";
import loadComponents from "./components/index";
import loadJoomlaComponents from "./joomla/index";
// import loadMegamenu from './menu/index';
import loadBlocks from "./blocks/index";
import loadPanels from "./panels/index";
import loadCanvas from "./canvas/index";
import extendStyleManager from "./style_manager/index";
import extendTraitManager from "./trait_manager/index";
import extendIcons from "./icons/index";

import loadUserblocks from "./userblocks/index";
import rteEditor from "./rich_text_editor/index";
// import rteEditor from "./rich_text_editor/rteEditor";


import utils from "./utils/index";
import initoolBar from "./components/initoolBar";

export default grapesjs.plugins.add(
  "grapesjs-plugin-t4",
  (editor, opts = {}) => {
    const options = {
        ...{
          // default options
        },
        ...opts
      },
      $ = editor.$;

    loadData(editor, options);

    // pluginPreset (editor, options.presetWebpage);

    // Add components
    loadComponents(editor, options);

    // Add Joomla components
    loadJoomlaComponents(editor, options);

    // Add loadMegamenu
    //loadMegamenu(editor, options);

    // Add blocks
    loadBlocks(editor, options);

    // Extend parnels
    loadPanels(editor, options);

    loadUserblocks(editor, options);
    initoolBar(editor, options);

    // Canvas
    loadCanvas(editor, options);

    // custom rte editor
    rteEditor(editor, options);

    // Canvas
    extendStyleManager(editor, options);
    extendTraitManager(editor, options);
    extendIcons(editor, options);


    // Do stuff on load
    editor.on("load", function() {
      var $ = grapesjs.$,
        pn = editor.Panels;

      // Load and show settings and style manager
      var openTmBtn = pn.getButton("views", "open-tm");
      openTmBtn && openTmBtn.set("active", 1);
      //var openSm = pn.getButton('views', 'open-sm');
      //openSm && openSm.set('active', 1);
      editor.Panels.removeButton("views", "open-tm");
      //editor.Panels.removeButton('views', 'open-blocks');
      //editor.Panels.removeButton('options','preview');
      // Add Settings Sector
      var traitsSector = $(
        '<div class="gjs-sm-sector no-select">' +
          '<div class="gjs-sm-title"><span class="icon-settings fa fa-cog"></span> Settings <i class="fa fa-caret-right"></i></div>' +
          '<div class="gjs-sm-properties" style="display: none;"></div></div>'
      );
      traitsSector.addClass('jpb-settings');
      var traitsProps = traitsSector.find(".gjs-sm-properties");
      traitsProps.append($(".gjs-trt-traits"));
      $(".gjs-sm-sectors").before(traitsSector);
      traitsSector.find(".gjs-sm-title").on("click", function() {
        var traitStyle = traitsProps.get(0).style;
        var hidden = traitStyle.display == "none";
        if (hidden) {
          traitStyle.display = "block";
          traitsSector
            .find(".gjs-sm-title i.fa")
            .removeClass("fa-caret-right")
            .addClass("fa-caret-down");
          traitsSector.addClass("gjs-sm-open");
        } else {
          traitStyle.display = "none";
          traitsSector
            .find(".gjs-sm-title i.fa")
            .removeClass("fa-caret-down")
            .addClass("fa-caret-right");
          traitsSector.removeClass("gjs-sm-open");
        }
      });

      // Open block manager
      var openBlocksBtn = editor.Panels.getButton("views", "open-Element");
      if (openBlocksBtn) {
        // disable view label + header trait
        const container = editor.$(".gjs-pn-views-container")[0];
        if (container && container.querySelector(".gjs-traits-label")) {
          container
            .querySelector(".gjs-traits-label")
            .parentNode.parentNode.remove();
        }
      }
      //check run active commands
      if(editor.StateManager.commandsActive){
        editor.StateManager.commandsActive.forEach( function(cmd, index) {
          let cmds = cmd;
          if(cmd.match(/set-device-/)) cmds = 'set-device';
          switch (cmds) {
            case 'open-blocks':
            case 'open-tm':
            case 'open-group-blocks':
            case 'open-sm':

              editor.Panels.getButton("views", "open-Element").set("active", 1);
              break;
             
            case 'sw-visibility':
              editor.Panels.getButton("options", "sw-visibility").set("active", true);
              break;

            case 'set-device':
              editor.Panels.getButton('devices-c', cmd).set('active',true);
              break;
              
            default:
              // statements_def
              break;
          }
        });
      }else{
        editor.Panels.getButton('devices-c', 'set-device-desktop').set('active',true);
        editor.Panels.getButton("options", "sw-visibility").set("active", true);
        editor.Panels.getButton("views", "open-Element").set("active", true);
      }
      //remove device select
      if (editor.$(".gjs-devices-c")) editor.$(".gjs-devices-c").remove();

      // active
      editor.Panels.getButton("options", "sw-visibility").set(
        "className",
        "fal fa-square"
      );
      editor.Panels.getButton("options", "fullscreen").set(
        "className",
        "fal fa-arrows-alt"
      );
      editor.Panels.removeButton("options", "export-template");
      editor.Panels.addPanel({ id: "crumbs-view", visible: true, buttons: [] });
      // Show logo with the version
      // <div class="gjs-logo-cont"><div class="gjs-logo-version"></div></div>
      var logoCont = document.createElement("div");
      logoCont.setAttribute("class", "gjs-logo-cont");
      logoCont.innerHTML =
        '<a href="#"><img class="gjs-logo" src="data:image/png;base64,"></a>';
      var logoPanel = document.querySelector(".gjs-pn-commands");
      logoPanel.appendChild(logoCont);
    });
  
    // On component change show the Style Manager
    editor.on("component:selected", model => {
      var $ = grapesjs.$;
      // Make text element to be in edit mode
      // if (model.get("type") == 'text') model.trigger("active");

      const openSmBtn = editor.Panels.getButton("views", "open-sm");
      const openLayersBtn = editor.Panels.getButton("views", "open-layers");
      // Don't switch when the Layer Manager is on or
      // there is no selected component
      if (
        (!openLayersBtn || !openLayersBtn.get("active")) &&
        editor.getSelected()
      ) {
        openSmBtn && openSmBtn.set("active", 1);
      }
      var settingsEl = $(".gjs-sm-sector.jpb-settings");
      //check not traits  disabled setting tab
      model.getTrait() ? settingsEl.removeClass('hidden') : settingsEl.addClass('hidden');
    });

    utils(editor, options);
    //disabled page block and save block
    editor.Panels.removeButton('views', 'open-Page');
    editor.Panels.removeButton('views', 'open-UserBlock');
    editor.editor.loadModule(require("./modules/template_manager"));
    editor.editor.loadModule(require("./modules/state_manager"));
    editor.editor.loadModule(require("./modules/breadcrumb_manager"));
    editor.editor.loadModule(require("./modules/revision_manager"));
    editor.editor.loadModule(require("./modules/help_manager"));
    editor.editor.loadModule(require("./modules/settings_manager"));
  }
);
