import _ from 'underscore';
const defaultCf =   {noPointer: ['commands','options','views','devices-c']};
module.exports = {

    getItemid(editor) {
        if (!this.itemid) this.itemid = editor.Config.itemId;
        return this.itemid;
    },
    getPanels(editor) {
        if (!this.panels) this.panels = editor.Panels.getPanelsEl();
        return this.panels;
    },

    tglPointers(editor, v) {
        var elP = editor.Canvas.getBody().querySelectorAll('.' + this.ppfx + 'no-pointer');
        _.each(elP, item => {item.style.pointerEvents = v ? '' : 'all';});
    },
    getContentRev(editor, itemid) {
        const url = this.getAjax(editor,'content',itemid);
        return fetch(url)
        .then(res => res.json())
        .then(data => {
            let parseData = JSON.parse(data.data);
            let components  = JSON.parse(parseData.components);
            let html        = parseData.html;
            let css         = parseData.css;
            if(css)  html   += "<style>"+css+"</style>" ;
            editor.setComponents(html);
            
        })
        .catch(err => {
            console.log(err);
        });
    },
    getAjax(editor,$task,itemid){
        let em = editor.editor;
        let $options = {};
        if($task) $options.data = $task;
        if(itemid) $options.itemid = itemid;
        return em.get('ajaxUrl').buildAjaxUrl('revision', $options);
    },
    pushDataRev(editor, itemid, options, revId = null, title = null) {
        const url = this.getAjax(editor,'save',itemid);
        fetch(url, {
            method: "POST",
             headers: {
                "Content-Type": "application/json",
                // "Content-Type": "application/x-www-form-urlencoded",
            },
            body: JSON.stringify({ revId, title, options })
        }).then(res => {
            this.stopRevision(editor);
            editor.Panels.getButton('views','open-revision').set('active',false);
            editor.Panels.getButton('views','open-Element').set('active',true);
            editor.toastr.success('Success');
            
        }).catch(err => {
            console.log(err);
            editor.toastr.error('Error!!!');
        });
    },
    saveDataRev(itemid, options, revId = null, title = null) {
        const url = this.getAjax(editor,'save',itemid);
        fetch(url, {
            method: "POST",
             headers: {
                "Content-Type": "application/json",
                // "Content-Type": "application/x-www-form-urlencoded",
            },
            body: JSON.stringify({ revId, title, options })
        }).then(res => {
            this.revRender(editor);
            editor.toastr.success('Success');
        }).catch(err => {
            console.log(err);
            editor.toastr.error('Error!!!');
        });
    },
    // disable all buttons in panel options
    disableButtonPn (editor) {
        const noPointer = defaultCf.noPointer;
        noPointer.forEach(pn => {
            editor.Panels.getPanel(pn).view.$el.addClass('gjs-no-pointer-events');
            editor.Panels.getPanel(pn).view.$el.removeClass('gjs-hidden');
        });
    },
    // disable all buttons in panel options
    reActiveButtonPn(editor) {
        const noPointer = defaultCf.noPointer;;
        noPointer.forEach(pn => {
            editor.Panels.getPanel(pn).view.$el.removeClass('gjs-no-pointer-events');
        });
    },
    //reset revision 
    RevReset (editor,itemid) {
        const url = this.getAjax(editor,'reset',itemid);
        return fetch(url)
        .then(res => res.json())
        .then(data => {
            let parseData   = JSON.parse(data.data);
            let components  = JSON.parse(parseData.components);
            let html        = parseData.html;
            let css         = parseData.css;
            if(css)  html   += "<style>"+css+"</style>" ;
            editor.setComponents(html);

        })
        .catch(err => {
            console.log(err);
        });
    },
    revDel(editor, id){
        const url = this.getAjax(editor,'revdel',id);
        fetch(url, {
            method: "POST",
             headers: {
                "Content-Type": "application/json",
                // "Content-Type": "application/x-www-form-urlencoded",
            },
            body: JSON.stringify({id})
        }).then(res => {
            this.revRender(editor);
            editor.toastr.success('Revision item deleted successfully!');
            
        }).catch(err => {
            console.log(err);
           editor.toastr.error('Revision item deleted failed!');
        });
    },
    revDelEl(editor, id) {
        let revDelConfig = editor.editor.config.revisionManager.revDel;
        if(!revDelConfig) return "";
         let that = this;
        let btnDel = document.createElement('button');
        btnDel.setAttribute('class', 'gjs-revision-del');
        btnDel.innerHTML = 'x';
        btnDel.addEventListener('click', function() {
            let alerts = confirm('Are you sure?');
            if (alerts == true) {
                that.revDel(editor, id);
            } else {
                return false;
            }
        });
        return btnDel;
    },
    //builder temp
    tempRev (editor, revData) {
        let that = this;

        const itemid = that.getItemid(editor);
        const container = editor.$('.gjs-pn-views-container')[0];
        if(container.querySelector('.gjs-filter-blocks')){container.querySelector('.gjs-filter-blocks').remove();}
        if(container.querySelector('.gjs-group-Revision')){
              container.querySelector('.gjs-group-Revision').remove();
        }
        const revisionEL = document.createElement('div');
        revisionEL.className = 'gjs-group-Revision';
        revisionEL.setAttribute('group','Revision');

        // create mockup revision
        //create revision header
        const revHeader = document.createElement('div');
        revHeader.setAttribute('class','gjs-revision-header');
        const revLabel = document.createElement('label');
        revLabel.innerHTML = 'Revision Manager';
        revHeader.appendChild(revLabel);

        // create footer revision
        let revFooter = document.createElement('div');
        revFooter.setAttribute('class','gjs-revision-footer');
        const btnSave = document.createElement('button');
        btnSave.setAttribute('class','gjs-revision-save fal fa-save');
        btnSave.innerHTML = "Save";
        btnSave.addEventListener('click',function(){
            // active autosave
            editor.StorageManager.getConfig().autosave = true;
            let title = prompt("Title : ", "current working");
            if(title !== null){
                that.saveDataRev(itemid,'insert', null, title);
            }else{
                return false;
            }

        });

        const btnApply = document.createElement('button');
        btnApply.setAttribute('class','gjs-revision-apply hidden');
        btnApply.innerHTML = "Apply";
        btnApply.addEventListener('click',function(){
            let revisionId = revisionEL.revId;
            let conf = confirm('Are you sure?!!');
            if(conf == true){
                 // active autosave
                editor.StorageManager.getConfig().autosave = true;
                that.pushDataRev(editor, itemid,'update', revisionId, null);
                
            }else{
                return false;
            }
        });
        revFooter.appendChild(btnApply);

        let checkCurrent = revData.filter(child => child.cur == '1');
        
        if(!checkCurrent.length){
            revFooter.appendChild(btnSave);
        }
        //create body revision
        const revBody = document.createElement('div');
        revBody.setAttribute('class','gis-revision-body');
        revisionEL.appendChild(revHeader);
        let revBodyChild = document.createElement('ul');
        revBodyChild.setAttribute('class','gis-revision-list');

        let current = document.createElement('li');
        current.setAttribute('class','gis-revision-item');
        current.classList.add('active');
        current.innerHTML = "<span><i class='far fa-clock'></i> current working<span>";
        current.addEventListener('click',function(){
                revBodyChild.querySelectorAll('.fa-clock').forEach(item => {
                    item.classList.remove('fa-clock');
                    item.classList.add('fa-history');
                    item.parentNode.parentNode.classList.remove('active');
                });
                revFooter.querySelector('.gjs-revision-apply').classList.add('hidden');
                revFooter.querySelector('.gjs-revision-apply').classList.remove('active');
                revFooter.querySelector('.gjs-revision-save').classList.remove('hidden');
                revFooter.querySelector('.gjs-revision-save').classList.add('active');
                this.classList.add('active');
                that.RevReset(editor,itemid);
                this.querySelector('.far').classList.remove('fa-history');
                this.querySelector('.far').classList.add('fa-clock');
        });

        if(!checkCurrent.length){
            revBodyChild.appendChild(current);
        }
        revData.forEach(rev => {
            let li = document.createElement('li');
            li.setAttribute('class','gis-revision-item');
            
            let liSpan = document.createElement('span');
            let title = rev.title ? rev.title : rev.ctime;
            li.appendChild(liSpan);
            let btnDel = that.revDelEl(editor, rev.id);
            if(rev.cur == '1'){
                title += ' (current working)';
                liSpan.innerHTML = '<i class="far fa-clock"></i> '+ title;
                li.classList.add('active');
            }else{
                liSpan.innerHTML = '<i class="far fa-history"></i> '+ title;
                btnDel && li.appendChild(btnDel);

            }

            liSpan.addEventListener('click',function(){
                editor.StorageManager.getConfig().autosave = false;

                revBodyChild.querySelectorAll('.fa-clock').forEach(item => {
                    item.classList.remove('fa-clock');
                    item.classList.add('fa-history');
                    item.parentNode.parentNode.classList.remove('active');
                });
                this.parentNode.classList.add('active');
                if(!checkCurrent.length){
                    revFooter.querySelector('.gjs-revision-save').classList.remove('active');
                    revFooter.querySelector('.gjs-revision-save').classList.add('hidden');
                }
                if(!rev.cur){
                    revFooter.querySelector('.gjs-revision-apply').classList.add('active');
                    revFooter.querySelector('.gjs-revision-apply').classList.remove('hidden');
                }else{
                    revFooter.querySelector('.gjs-revision-apply').classList.add('hidden');
                }
                revisionEL.revId = rev.id;
                that.getContentRev(editor,rev.id);
                this.querySelector('.far').classList.remove('fa-history');
                this.querySelector('.far').classList.add('fa-clock');

            });
            revBodyChild.appendChild(li);
        });
        revBody.appendChild(revBodyChild);
        const btnBack = document.createElement('button');
        btnBack.setAttribute('class','gjs-revision-back');
        btnBack.innerHTML = '<i class="fal fa-long-arrow-left"></i> Back';
        btnBack.addEventListener('click',function(){
            that.stopRevision(editor);
            editor.Panels.getButton('views','open-revision').set('active',false);
            editor.Panels.getButton('views','open-Element').set('active',true);
        });
        revFooter.appendChild(btnBack);
        revisionEL.appendChild(revBody);
        revisionEL.appendChild(revFooter);
        container.prepend(revisionEL);
        if(container.querySelector('.gjs-block-header')){container.querySelector('.gjs-block-header').style.display = 'none';}
    },
    revRender(editor) {
        const itemid = this.getItemid(editor);
        const url = this.getAjax(editor,'',itemid);
        fetch(url)
        .then(res => res.json())
        .then(data => {
            this.tempRev(editor,data);
        })
        .catch(err => {
            console.log(err);
        });
    },
    run(editor, sender) {
        sender && sender.set('active',true);
        // disable autosave
        editor.StorageManager.getConfig().autosave = false;
        editor.runCommand('preview');
        editor.$('.gjs-cv-canvas').get(0).style = '';
        //remove button preview
        editor.$('.gjs-off-prv').remove();
        editor.$('.gjs-pn-panels').get(0).style.display = 'block';
        //gjs-no-pointer-events
        this.disableButtonPn(editor);
        editor.Panels.getPanel("views-container").view.$el.removeClass('gjs-hidden');
        this.revRender(editor);
    },
    
    stop(editor, sender){sender && sender.set('active',false);},

    stopRevision(editor) {
        const itemid = this.getItemid(editor);
        this.RevReset(editor,itemid);
        // active autosave
        editor.StorageManager.getConfig().autosave = true;
        editor.stopCommand('preview');
        this.reActiveButtonPn(editor);
        const container = editor.$('.gjs-pn-views-container')[0];
        if(container.querySelector('.gjs-group-Revision')){
            container.querySelector('.gjs-group-Revision').remove();
            container.querySelector('.gjs-block-header').style.display = 'block';
        }
    }
};
