import {
  typeIcons,
} from './config';

export default (editor, opts = {}) => {
  const bm = editor.BlockManager;
  const { blockIcons, blockLabel } = opts;
  var loadIcon = editor.Config.loadIcons;
  if(!loadIcon.material_icons.material_icons && !loadIcon.awesome_icons.awesome_icons) return;
  blockIcons && bm.add('icons', {
    label: `icons`,
    attributes: {class:'fal fa-cubes'},
    category: 'Basic',
    group: 'Element',
    activate: true,
    select: true,
    content: '<i class="material-icons">home</i>',
    ...blockIcons
  });
}