import { isArray, isString } from 'underscore';

export default(model, config) => {
	const traits = model.get('traits');
	// if (!config || !config.traits) return;
	model.set({
		selectable: true
	});

	if (config.draggable) model.set('draggable', config.draggable)


	config.model && config.model.traits && config.model.traits.forEach(t => {
		const name = isString(t) ? t : t.name;
		if (!name) return;
		if (traits.find({name})) return;
		const temp = traits.add(t),
			trait = isArray(temp) ? temp[0] : temp;

		if (trait.get('isClass')) {
			trait.set('changeProp', 1);
			detectClass(model, trait);
		}

	})
		
}


const updateTraits = () => {
    const event = 'change:device';
    const toListen = [this.em, event, this.updateTraits];
    this.stopListening(...toListen);

    if (this.traitsOnDevice == undefined) {
        this.traitsOnDevice = false;
        const traits = [...this.get('traits').toArray()];
        traits.forEach(trait => {
            if (trait.get('device')) this.traitsOnDevice = true;
        })
        if (this.traitsOnDevice) this.traits = traits;
    }

    if (this.traitsOnDevice) {
        let traits = [],
            device = editor.getDevice() || 'Desktop';

        this.traits.forEach(trait => {
            if (trait.get('device') && trait.get('device').indexOf(device) == -1) return;
            traits.push(trait);
        })

        this.set('traits', traits);
        this.em.trigger('component:toggled');

        this.listenTo(...toListen);
    }
}

const detectClass = (model, trait) => {
    const name = trait.get('name');
	switch(trait.get('type')) {
		case 'select':
		    const classes = trait.get('options').map(opt => typeof opt == 'string' ? opt : opt.value);

		    model.getClasses().forEach(cls => {
		        if (classes.indexOf(cls) >= 0) {
		            model.set(name, cls);
		        }
		    })

		    model.on('change:' + name, () => {
		        const clss = model.getClasses(),
		            prevCls = model.previous(name),
		            cls = model.get(name),
		            idx = clss.indexOf(prevCls);

		        if (idx > -1) clss[idx] = cls;
		        else clss.push(cls);
		        model.setClass(clss);
		    })		
			break;
		case 'checkbox':
			const cls = trait.get('val');

	        if (model.getClasses().includes(cls)) {
	            model.set(name, true);
	        }

		    model.on('change:' + name, () => {
		        if (model.get(name)) {
		        	model.addClass(cls);
		        } else {
		        	model.removeClass(cls);
		        }
		    })	
			break;
	}

}
