export function parseOverlay(model) {
	const overlay = {};
	// parse overlay
	const omodel = model.components().find(c => c.getClasses().indexOf('t4b-bg-overlay') != -1);
	if (omodel) {
		const el = omodel.view.el;
		const match = el.className.match(/(^|\s)t4b-bg-overlay-(\w+)(\s|$)/i);
		
		overlay.type = match ? match[2] : 'none';
		
		switch(overlay.type) {
            case 'image':
                const style = el.currentStyle || window.getComputedStyle(el, false);
                overlay.url = style.backgroundImage.slice(4, -1).replace(/"/g, "");
                overlay.size = style.backgroundSize;
                overlay.position = style.backgroundPosition;
                overlay.repeat = style.backgroundRepeat;
                overlay.attachment = style.backgroundAttachment;
                overlay.opacity = parseFloat(style.opacity);
                break;
            case 'video':
                const overlayCon = el.querySelector('.bg-overlay-vid');
                if (!overlayCon) break;
                const vidIfr = overlayCon.querySelector('iframe');
                if (!vidIfr) break;
                // opacity
                var style = el.currentStyle || window.getComputedStyle(el, false);
                overlay.opacity = parseFloat(style.opacity) * 100;
                // video src
                const src = vidIfr.getAttribute('src');
                let match;
                if (match = src.match(/player\.vimeo\.com\/video\/(.+)\?/)) {
                    overlay.video = 'vimeo';
                    overlay.id = match[1];
                }
                if (match = src.match(/www\.youtube\.com\/embed\/(.+)\?/)) {
                    overlay.video = 'youtube';
                    overlay.id = match[1];
                }
                break;
        }
	}

	return overlay;
}

export function buildOverlay(model) {
	const overlay = model.get('overlay');
	// update overlay
	let omodel = model.components().find(c => c.getClasses().indexOf('t4b-bg-overlay') != -1);
	switch(overlay.type) {
		case 'image':
		case 'video':
		case 'file':
            // create overlay component
            const opt = {noIncrement: 1, at: 0},
                com = {
                    type: 't4.ghost',
                    classes: 't4b-bg-overlay t4b-bg-overlay-' + overlay.type
                };
			if (omodel) {
                model.components().remove(model.components().at(0));
			}
            omodel = model.components().add(com, opt);
			const func = 'build' + overlay.type.toLowerCase();
			eval(func)(omodel, overlay);
			break;
		default:
			// remove model if existed
			if (omodel) omodel.remove();
			break;		
	}
}



const buildimage = (model, overlay) => {
    model.set('content', '&nbsp;');
    model.components().reset();
    var styles = {};
    styles['background-image'] = `url(${overlay.url})`;
    if (overlay.size) styles['background-size'] = overlay.size;
    if (overlay.position) styles['background-position'] = overlay.position;
    if (overlay.repeat) styles['background-repeat'] = overlay.repeat;
    if (overlay.opacity) styles['opacity'] = overlay.opacity;
    model.setStyle(styles);
}

const buildvideo = (model, overlay) => {
    model.set('content', '');
    let src;
    switch(overlay.video) {
        case 'vimeo':
            src = '//player.vimeo.com/video/' + overlay.id + '?title=0&amp;byline=0&amp;portrait=0&amp;autoplay=1&amp;loop=1&amp;mute=1';
            break;
        case 'youtube':
            src = '//www.youtube.com/embed/' + overlay.id + '?playlist=' + overlay.id + '&amp;rel=0&amp;controls=0&amp;showinfo=0&amp;autoplay=1&amp;loop=1&amp;html5=1&amp;mute=1';
            break;
    }

    model.setStyle({});
    model.components().reset();

    // add video container
    const videoCon = model.components().add('<div class="bg-overlay-vid" t4-nosel></div>');
    // add video iframe
    const vidIfr = videoCon.components().add(`<iframe frameborder="0" width="100%" height="100%" allowfullscreen="" mozallowfullscreen="" webkitallowfullscreen="" src="${src}"></iframe>`);
    vidIfr.set({
        selectable: false
    });

    var styles = {};
    if (overlay.opacity) styles.opacity = overlay.opacity;
    videoCon.setStyle(styles);
    videoCon.set({
        selectable: false
    });
}


const buildfile = (model, overlay) => {
    let src;
    if(overlay.cover){
        src = overlay.cover;
    }else if(overlay.webm){
        src = overlay.webm;
    }
    else if(overlay.mp4){
        src = overlay.mp4;
    }
    else if(overlay.ogg){
        src = overlay.ogg;
    }else{
        src = '//www.youtube.com/embed/';
    }
    model.setStyle({});
    model.components().reset();
    const fileCon = model.components().add('<div class="bg-overlay-vid" t4-nosel></div>');
    const Filefr = fileCon.components().add(`<video loop="1" muted="1" autoplay="1" src="${src}" type="video/mp4" poster=""></video>`);
    Filefr.set({
        selectable: false
    });
    var styles = {};
    if (overlay.opacity) styles.opacity = overlay.opacity;
    fileCon.setStyle(styles);
}