export default {
	extend: 't4.element',
	model: {
		defaults: {
            smgroup: 'text',
            name:"Box",
            selectable:true,
            editable:true,
            draggable:true,
            highlightable:true,
            droppable:true,
			attributes: {
                class: 't4b-box'
            }
		},
		init(){
			this.em.getEditor().DomComponents.getType('default').model.prototype.init.apply(this, arguments);
            if (this.opt.temporary) return;
		}
	},
	isComponent(el) {
		return el.className && el.classList.contains('t4b-box');
	}
}