module.exports = () => {

  return {
    /**
     * Name of the module
     * @type {String}
     * @private
     */
    name: 'StateManager',
    storageKey: ['commands', 'states', 'blockscss'],
    states: {},

    /**
     * Initialize module
     */
    init(opts = {}) {
      this.em = opts.em;
      this.commands = this.em.get('Editor').Commands;
      this.em.get('Editor').StateManager = this;
      return this;
    },

    get(name) {
      return this.states[name];
    },

    set(name, value) {
      if (this.states[name] == value) return;
      this.states[name] = value;
      this.em.store();
    },

    store(noStore) {
      let store = {};
      store.commands = Object.getOwnPropertyNames(this.em.get('Editor').Commands.getActive());
      store.states = {...this.states};
      store.blockscss = this.getBlocksCss();
      store.blocksjs = this.getBlocksJs();
      return store;
    },

    getBlocksCss() {
      const BlockIds = this.em.get('Editor').getWrapper().findType('t4.section').map(com => com.getAttr && com.getAttr('t4-bid'));
      const ElementIds = this.em.get('Editor').getWrapper().findType('t4.element').map(com => com.get('bid') || (com.getAttr && com.getAttr('t4-bid')));
      let blockIds = [...new Set([...BlockIds, ...ElementIds])];
      let css = '';
      this.em.get('t4-config').blocks.filter(bl => {
        if (blockIds.indexOf(bl.id) >= 0 && bl.css) css += bl.css + '\n';
      });
      return css;
    },
    getBlocksJs() {
      const blockIds = this.em.get('Editor').getWrapper().findType('t4.section').map(com => com.getAttr && com.getAttr('t4-bid'));
      let js = '';
      this.em.get('t4-config').blocks.filter(bl => {
        if (blockIds.indexOf(bl.id) >= 0 && bl.js) js += bl.js + '\n';
      });
      return js;

    },


    load(data) {
      // active options buttons
      if (data.commands) {
        const buttons = this.em.get('Editor').Panels.getPanel('options').get('buttons');
        buttons.each(btn => {
          if (btn.get('id') == 'fullscreen') return;

          const active = data.commands.indexOf(btn.get('id')) > -1;
          btn.set('active', active);

          // btn.on('change:active', () => {
          //   if (btn.get('active') != btn.previous('active')) this.em.store();
          // });
        });
      }

      this.states = {...data.states};
      this.commandsActive = data.commands;

      this.em.trigger('state:load');
    }

  };
};
