import { commandNameIcons, keyIcons, tabIcon, commandIconView } from './config';

export default (editor, opts = {}) => {
  const cmd = editor.Commands;
  const { modalTitle, codeViewOptions, commandIcons } = opts;
  const modalIcons = editor.Modal;
  const toggle = (target) =>{
    if(target.hasClass('show')){
      target.removeClass('show');
      target.addClass('hidden');
    }else{
      target.addClass('show');
      target.removeClass('hidden');
    }
  }
  const tabActive = (target) =>{
    let $ = editor.$;
    let tab = $(target);
    let tabCurrentactive =  $(target).parents('ul').find('.active');
    let href = target.getAttribute('href');
    let currentHref = tabCurrentactive.get(0).getAttribute('href');
    let tabContent = $(href);

    if(href === currentHref) return;
    let currentTabAct = tabContent.parents('.t4b-tabs-content').find('.show.active');
    currentTabAct.removeClass('show active');
    currentTabAct.addClass('fade');
    tabContent.addClass('show active');
    tabContent.removeClass('fade')
    tab.addClass('active');
    tabCurrentactive.removeClass('active');

  }
  // Add the custom code command
  var parentEditables, iconsData;
  cmd.add(commandNameIcons, {
    keyIcons,
    tabIcon,
    commandIconView,

    run(editor, sender, opts = {}) {

      this.editor = editor;
      this.options = opts;
      this.target = opts.target || editor.getSelected();
      const target = this.target;

      if (target && target.get('editable')) {
        // Disable parent text element, to make sure it will not update 
        parentEditables = [];
        var parent = target.parent();
        while (parent && parent.get('editable')) {
          parentEditables.push(parent);
          parent.set('editable', false);
          parent = parent.parent();
        }

        this.showIcons(target);
      }
    },

    stop(editor) {
      this.target = opts.target || editor.getSelected();
      const target = this.target;

      editor.Modal.close();
      
      // restore parent text element
      if(parentEditables) {
        setTimeout(() => {
          parentEditables.forEach(p => p.set('editable', true))
          parentEditables = null;
        }, 50)
      }
    },

    /**
     * Method which tells how to show the custom code
     * @param  {Component} target
     */
    showIcons(target) {
      const { editor, options } = this;
      const title = options.title || modalTitle;
      let that = this;
      modalIcons.setTitle(title);
      const content = document.createElement('div');
      const pfx = editor.getConfig('stylePrefix');
      content.className = `${pfx}icons`;

      if (iconsData) {       
        content.innerHTML = iconsData;
        that.eventTabs(content);
        modalIcons.setContent(content);

        //init tab active when modal open
        that.setTabActive(modalIcons.getContentEl());
        modalIcons
        .open()
        .getModel()
        .once('change:open', () => editor.stopCommand(this.id));        
      } else {
        this.fetchIcons(target)
      }
    },

    fetchIcons(target) {
      const url = this.em.get('ajaxUrl').buildAjaxUrl('loadicons');
      fetch(url)
        .then(res => res.json())
        .then(data => {
          iconsData = data.data;
          this.showIcons(target);
        }).catch(err => {
          console.log(err);
        })
    },

    eventTabs(modalEl){
      const { editor, target } = this;
      let that = this;
      let $ = editor.$;
      let btns = modalEl.querySelectorAll('.nav-link');
      let icons = modalEl.querySelectorAll('.t4b-icon');
      
      if(icons.length){
        icons.forEach( icon => {
          /*icon.addEventListener('dblclick', function (e) {
            e.preventDefault();
            that.handleSave(this);
          });*/
          icon.onclick = function (e) {
            e.preventDefault();
            that.handleSave(this);
          };
        });
      }
      if(btns.length){
        btns.forEach(btn => {
          btn.onclick = function(e){
            e.preventDefault();
            tabActive(this);
          };
        })
      }

    },
    /**
     * Handle the main tab active task
     */
    setTabActive(modalEl){
      const { editor, target} = this;
      //add active tab and icon
      const icon = target.get(keyIcons) || '';
      const tabIcon = target.get('tabicon') || '';
      let tabAct = '';
      if(tabIcon == 'fa'){
        tabAct = modalEl.querySelector('#awesome-tab');
      }else if(tabIcon == 'material'){
        tabAct = modalEl.querySelector('#material-tab');
      }
      tabAct && tabAct.click();
    },
    /**
     * Handle the main save task
     */
    handleSave(element) {
      const { editor, target } = this;
      let icon = element.getAttribute('data-val');
      let tabIcon = element.getAttribute('data-tab');
      target.set(tabIcon, tabIcon);
      target.set(keyIcons, icon);
      editor.Modal.close();
    },
    ...commandIcons,
  });
};
