import domstylable from "./domstylable";
import extendOverlayManager from './overlays/index';
import extendFontFamily from './font_family/index';
import imageBG from './image_bg/index';

export default (editor, config = {}) => {
    // extend stylable/unstylable for DOM component
    domstylable(editor, config);
    extendOverlayManager(editor, config);
    extendFontFamily(editor, config);
    imageBG(editor, config);

    const createSites = (top, right = top, bottom = top, left = right) => {
        return `${top} ${right} ${bottom} ${left}`;
    };

    const parseValue = val => {
        const sides = (val || "0").split(/\s+/);
        return createSites(...sides);
    };

    const buildValue = fullval => {
        const arr = fullval.split(/\s+/);
        if (arr.length != 4) return arr;
        const [top, right, bottom, left] = arr;
        const sides = [];

        if (
            sides.push(top) &&
            top == right &&
            right == bottom &&
            bottom == left
        )
            return sides;
        if (sides.push(right) && top == bottom && left == right) return sides;
        if (sides.push(bottom) && left == right) return sides;
        sides.push(left);
        return sides;
    };
    

    var currentSMGroup = '';
    editor.on('component:selected', model => {

        const sm = editor.StyleManager,
            smgroup = model.get('smgroup') || 'box',
            group = model.getAttributes()['t4-group'];
            
        var csm;

        if (smgroup + ':' + group == currentSMGroup) return;

        try {
            csm = [...require('./config/' + smgroup).default]
        } catch (e) {
            csm = [...require('./config/box').default]
        }

        // overlay
        if (group == 'Block') {
            csm.push({
                name: 'OverLays',
                id: 'OverLays',
                open: false,
                properties:[
                    {name:'Overlay type', type:'OverlayProp'}
                ]
            })
        }
        sm.getSectors().reset(csm);
        currentSMGroup = smgroup + ':' + group;
        // make block categories view as accordion
        const sectors = sm.getSectors();
        let sectorsAct = sectors.find(st => st.get('open') == true);
        sectors.each(sector => {
            sector.set('open', false).on('change:open', opened => {
                opened.get('open') && sectors.each(sector => {
                    sector !== opened && sector.set('open', false)
                })
            })
        })
        sectorsAct && sectorsAct.set('open',true);
    });

    // load default styleManager
    editor.on("load", () => {

        //get body add class page
        editor.getWrapper().addClass('jpb-page');

        // custom style manager
        const sm = editor.StyleManager;
        const csm = [...require('./config/body').default]
        if (csm && csm.length) sm.getSectors().reset(csm);
    });
};
