export default (editor, config = {}) => {
	let videoSource,videoID,Background_image,Background_size,Background_position,Background_repeat,Background_attachment,Cover,Webm,mp4,OGG,Opacity;
	const sm = editor.StyleManager;
	const clearProp = 'data-clear-style';
	const { propertys } = config;

	const selectType = sm.getType('select');
	const selectModel = selectType.model;
	const selectView = selectType.view;
	sm.addType('OverlayProp', {
		model: selectModel.extend({
			defaults: {
				...selectModel.prototype.defaults,
				name: 'Overlay Type',
				property: 'OverlayProp',
				type: 'select',
				defaults: 'none',
				options: [
					{value: 'none',name: 'none'},
					{value: 'image',name: 'Image'},
					{value: 'video',name: 'Video'},
					{value: 'file',name: 'File'},
				],
				full: 1,
				info: 'Overlays',
				icon: '',
				functionName: '',
				status: '',
				visible: false,
				fixedValues: ['initial', 'inherit'],
			},
			init(){
				let selected = editor.getSelected();
				const overlays = selected.getOverlay();
				this.set('Overlays',overlays);
			},

			getOptions() {
				const { options,list } = this.attributes;
				return options && options.length ? options : list;
			},
			updatePropsOptions() {
				const value = this.get('Overlays');
				const PropsOL = value || {type:'none'};
				if (typeof PropsOL === "object" && PropsOL !== null) {
					this.updateAttrEL(PropsOL);
				}
			},
			updateAttrEL(value) {
				let selected = editor.getSelected();
				selected && (typeof selected.setOverlay === 'function') && selected.setOverlay(value);
				
			},
			setValueOverLay(val,opts={}) {
				
				if(val){
					let check_prev = this.get('Overlays') || {};
					const val_prev = check_prev.type;
					let Overlays = {};
					Overlays['type'] = val;
					switch (val) {
						case 'video':
							Overlays['video'] = 'youtube';
							break;
						case 'image':
							break;
						default:
							// statements_def
							break;
					}
					if(val_prev == val){
						this.set('Overlays',check_prev);
						this.setValue(val_prev,1);
					}else{
						this.set('Overlays',Overlays);
					}
				}else{
					this.set('Overlays',{type:'none'});
				}
					this.updatePropsOptions();

			},
		    setValue(value, prev = 0 , complete = 1, opts = {}) {
		      	const parsed = this.parseValue(value);
		      	this.set(parsed, { ...opts, avoidStore: 1 });
				if (complete) {
					this.set('value', '', opts);
					this.set(parsed, opts);
				}
				videoSource && videoSource.setValue(this.findValueProps('video'));
				videoID && videoID.setValue(this.findValueProps('id'));
				Opacity && Opacity.setValue(this.findValueProps('opacity'));
				Background_image && Background_image.setValue(this.findValueProps('url'));
				Background_size && Background_size.setValue(this.findValueProps('size'));
				Background_position && Background_position.setValue(this.findValueProps('position'));
				Background_repeat && Background_repeat.setValue(this.findValueProps('repeat'));
				Background_attachment && Background_attachment.setValue(this.findValueProps('attachment'));
				Cover && Cover.setValue(this.findValueProps('cover'));
				Webm && Webm.setValue(this.findValueProps('webm'));
				mp4 && mp4.setValue(this.findValueProps('mp4'));
				OGG && OGG.setValue(this.findValueProps('ogg'));
		    },
			findValueProps(name,prev = 0) {
				let result = null;
				let propsObj = this.get('Overlays');
				const findValue = propsObj[name];
				
				if (findValue) {
					result = findValue;
				}
				let posistion = '';
		      	if(name == 'position'){
		      		switch (result) {
		      			case '0% 0%':
		      			case '0px 0px':
		      				result = 'left top';
		      				break;
		      			case '0% 50%':
		      				result = 'left center';
		      				break;
		      			case '50% 0%':
		      				result = 'center top';
		      				break;
		      			case '100% 50%':
		      				result = 'right center';
		      				break;
		      			case '50% 100%':
		      				result = 'center bottom';
		      				break;
		      			case '50% 50%':
		      				result = 'center center';
		      				break;
		      			case '100% 100%':
		      				result = 'right bottom';
		      				break;
		      			defaults:
		      			   result = result;
		      			   break;
		      		}
		      	}
				return result;
			}

		}),
		view: selectView.extend({
			
			templateInput() {
				const pfx = this.pfx;
				const ppfx = this.ppfx;
				return `
                   <div class="${ppfx}field ${ppfx}select property-overlays">
                     <span id="${pfx}input-holder"></span>
                     <div class="${ppfx}sel-arrow">
                       <div class="${ppfx}d-s-arrow"></div>
                     </div>
                   </div>
                  `;
			},
			initialize(...args) {
			    selectView.prototype.initialize.apply(this, args);

		  	},
			inputValueChanged(e) {
				e && e.stopPropagation();
				let value = this.getInputValue();
				this.model.setValue(value, 1, { fromInput: 1 });
				this.model.setValueOverLay(value);
			},
			getTargetValue(opt = {}) {
				let result = "";
				let  targetOverlay = {};
				const target = editor.getSelected();
				if (!target) {
					return result;
				}
				if(target && (typeof target.getOverlay === 'function')){
					targetOverlay = target.getOverlay();
				}

				if (typeof targetOverlay === 'object' && targetOverlay !== null) {
					this.model.set('prev_ol',targetOverlay);
					this.model.set('Overlays',targetOverlay);
					result = targetOverlay.type;
				}
				return result;
			},
			modelValueChanged(e, val, opt = {}) {
				const model = this.model;
				const value = model.get('value');
				this.renderByOverlay(value);
				this.render();
			},
			renderByOverlay(value) {
				var prop_child;
				switch (value) {
					case 'video':
						prop_child = propertys.video;
						break;
					case 'image':
						prop_child = propertys.img;
						break;
					case 'file':
						prop_child = propertys.file;
						break;
					case 'none':
					default:
						prop_child = '';
						break;

				}
				this.model.set('OL_Props', prop_child);
			},
			render() {
				selectView.prototype.render.apply(this, arguments);
				let conf_Props = this.model.get('OL_Props');
				if (conf_Props) {
					const mod = this.model;
					conf_Props.forEach(prop => {
						const propName = prop.oltype;
						const type = prop.type;
						const inputObj = {};
						const propinput = sm.createType(type, {
							model: {...prop,...inputObj}
						});
						propinput.render();
						propinput.model.on('change:value', (model, value) => {
							let	overl = mod.get('Overlays');
							overl[propName] = value;
							mod.set('Overlays', overl);
							mod.updatePropsOptions();
						})
						this.el.appendChild(propinput.el);
						propName == 'id' && (videoID = propinput);
						propName == 'video' && (videoSource = propinput);
						propName == 'opacity' && (Opacity = propinput);
						propName == 'url' && (Background_image = propinput);
						propName == 'size' && (Background_size = propinput);
						propName == 'position' && (Background_position = propinput);
						propName == 'repeat' && (Background_repeat = propinput);
						propName == 'attachment' && (Background_attachment = propinput);
						propName == 'cover' && (Cover = propinput);
						propName == 'webm' && (Webm = propinput);
						propName == 'mp4' && (mp4 = propinput);
						propName == 'ogg' && (OGG = propinput);
					});
				}
				this.setValue(this.model.get('value'), { targetUpdate: 1 });
			}
		}),
	});
}