export default (editor, config = {}) => {
    var siteUrl = config.siteUrl;
    var cmd = editor.Commands;
    //get all name of UserBlocks
    var $allUserBlock = editor.editor.get('t4-config').blocks.filter(el => el.category == 'UserSaveBlock');
    var $allShareBlock = editor.editor.get('t4-config').blocks.filter(el => el.category == 'UserShareBlock');
    //end get all name of block
    const shareTmpl = (editor,$allShareBlock) => {
        var modalEl = document.createElement('div');
            modalEl.innerHTML = `
                <form action="" id="t4_saveblock_form">
                    
                    <div class="form-check">
                        <div class="form-title">Share Block:</div>
                        <input class="form-check-input" type="checkbox" value="" id="shareblock" name="shareblock">
                        <label class="form-check-label" for="shareblock" >Make as share block</label>
                    </div>
                    <div class="form-row">
                        <div class="cat_bock">
                            <label for="category_block" class="cate_block form-title">Block Name:</label>
                            <div>
                                <select name="category" id="category_block">
                                    <option value="">Add New</option>
                                </select>
                                <input placeholder="save to user's Block" id="addnew" class="addnew" type="text" name="addnew" value="" />
                            </div>
                        </div>
                    </div>
                    <div>
                        <input  class="cancel btn" type="submit" value="cancel" name="submit"/>
                        <input  class="saveblock btn" type="submit" value="Save" name="submit"/>
                </form>
                `;
        var checked = '';
        var $checked = false;
        modalEl.querySelector('#shareblock').onclick = () => {
            $checked = modalEl.querySelector('#shareblock').checked;
            var $checkBlock = '';
            var $cate = '';
            var $option = '<option value="">Add New</option>';
            if ($checked === true) {
                $cate == 'UserShareBlock';
                $checkBlock = $allShareBlock;
                if ($checkBlock.length) {
                    for (var i = $checkBlock.length - 1; i >= 0; i--) {
                        $option += "<option value=\"" + $checkBlock[i].name + "\" >" + $checkBlock[i].name + "</option>";
                    }
                }
            }
            modalEl.querySelector('#category_block').innerHTML = $option;
        }
        modalEl.querySelector('#category_block').onchange = () => {
            var valueChange = modalEl.querySelector('#category_block').value;
            if(valueChange){
                modalEl.querySelector('.addnew').classList.add('hidden');
                checked = 'O';
            }else{
                modalEl.querySelector('.addnew').classList.remove('hidden');
                checked = 'A';
            }
            
        }
        modalEl.querySelector('.cancel').onclick = (e) => {
            e.preventDefault();
            editor.stopCommand('save-block');
        }
        modalEl.querySelector('.saveblock').onclick = (e) => {
            $checked = modalEl.querySelector('#shareblock').checked;
            const models = editor.getSelected();
            var name = '';
            if (checked == 'O' && $checked == true) {
                name = modalEl.querySelector('#category_block').value;
            } else {
                name = modalEl.querySelector('#addnew').value;
            }
            var category = '';
            var content = '';
            var InlineCss = editor.CodeManager.getCode(editor.getSelected(), 'css', { cssc: editor.CssComposer });
            
            var $checked = modalEl.querySelector('#shareblock').checked;
            //save block to category in panels
            if ($checked === true) {
                category = 'UserShareBlock';
                content = '<div class="inner_edit">' + models.toHTML() + '</div>';
            } else {
                category = 'UserSaveBlock';
                content = models.toHTML();
            }
            content += "<style>" + InlineCss + "</style>";
            const bm = editor.BlockManager;
            const t4Block = editor.editor.get('t4-config').blocks;
            let urls = buildAjaxUrl('saveblock',{name:name,category:category});
            fetch(urls, {
                method: "POST",
                 headers: {
                    "Content-Type": "application/json",
                    // "Content-Type": "application/x-www-form-urlencoded",
                },
                body: JSON.stringify({ content })
            }).then(res => {
                if (checked == 'O' && $checked == true) {
                    var $id_block = editor.editor.get('t4-config').blocks.filter(els => els.category == category).find(el => el.name == name).id;
                    editor.BlockManager.remove($id_block);
                }
                if (category === 'UserShareBlock') {
                    bm.add(name, {
                        label: name,
                        id: name,
                        name: name,
                        attributes: { blockname: name, class:'fal fa-empty-set' },
                        group: "UserBlock",
                        category: category,
                        content: {
                            type: 'shareblock',
                            attributes: {
                                blockname: name
                            }
                        },
                        data_content: content
                    });
                    t4Block.push({
                        label: name,
                        id: name,
                        name:name,
                        group: "UserBlock",
                        attributes: { type: "shareblock", blockname: name, class:'fal fa-empty-set' },
                        category: category,
                        content: {
                            type: 'shareblock',
                            attributes: {
                                blockname: name
                            }
                        },
                        data_content: content
                    });
                    models.get('components').reset();
                    const changeModel = models.replaceWith({
                            type: 'shareblock',
                            attributes: {
                                blockname: name
                            }
                        });
                    changeModel.get('components').reset(content);
                } else {
                    bm.add(name, {
                        label: name,
                        id: name,
                        name:name,
                        group: "UserBlock",
                        category: category,
                        content: content,
                    });
                    t4Block.push({
                        label: name,
                        id: name,
                        name:name,
                        group: "UserBlock",
                        category: category,
                        content: content,
                    });
                }

                alert('success');
            }).catch(err => {
                console.log(err);
                alert('false');
            });
            e.preventDefault();
            editor.stopCommand('save-block');
        }
        return modalEl;
    }
    cmd.add('save-block',{
        run: function (editor, sender) {
            const modal = editor.Modal;
            let modalEls = shareTmpl(editor,$allShareBlock);
            modal.setContent(modalEls);
            modal.setTitle('Save to User Blocks');
            modal.open();
            //fix display and close when click outside 
            let $content = editor.$(modal.getContentEl()).parents('.gjs-mdl-container');
            let divMask = document.createElement('div');
            divMask.setAttribute('class','saveblock-mask');
            let checkMask = $content.find('.saveblock-mask');
            
            divMask.addEventListener('click', function(e) { e.preventDefault()});
            if(!checkMask.length){
                $content.get(0).appendChild(divMask);
            }
            $content.find('.gjs-mdl-btn-close').get(0).style.display = 'none';
            if ($content.find('.gjs-mdl-btn-fulwith').length) $content.find('.gjs-mdl-btn-fulwith').remove();
        },
        stop: function (editor, sender) {
            const modal = editor.Modal;
            let $content = editor.$(modal.getContentEl()).parents('.gjs-mdl-container');
            if($content.find('.saveblock-mask')){
                $content.find('.saveblock-mask').remove();
            }
            editor.Modal.close();
        }
    });
    // extend toolbar
    editor.on('component:selected', model => {
        if (model.presave) return;
        var tb = model.get('toolbar');
        
        // for container tag only
        const tagName = model.getEl().tagName;
        
        if (['DIV', 'NAV', 'SECTION', 'HEADER', 'FOOTER'].indexOf(tagName) == -1) return;
        // must clone able
        if (!model.get('copyable') && (['carousels','tabs',"icon"].indexOf(model.get('type')) == -1)) return;
        //add editor on component selected
        var t4_tb_editor = false;
        // check selected is block
        if(editor.getComponents().findIndex(editor.getSelected()) === -1){
            t4_tb_editor = true;
        }
        //if(!attrT4) t4_tb_editor = true;
        tb.forEach(el => {
            if (el.attributes.class.split(" ")[1] == 'fa-code') {
                t4_tb_editor = true;
            }
            return t4_tb_editor;
        })
        if (t4_tb_editor == false) {
            tb.push({
                attributes: { 
                    class: 'fal fa-code',
                    title: '',
                    'data-tooltip': 'Block Editor',
                    'data-tooltip-pos': 'bottom'
                },
                command: 'block-editor',
                id: 'block-editor',
            });
        }

        var t4_tb_save = false;
        tb.forEach(el => {
            if (el.attributes.class.split(" ")[1] == 'fa-save') {
                t4_tb_save = true;
            }
            return t4_tb_save;
        })
        if (t4_tb_save == false) {
            /*tb.push({
                name: 'save-btn',
                attributes: { 
                    class: 'fal fa-save',
                    title: '',
                    'data-tooltip': 'Save to Changes',
                    'data-tooltip-pos': 'bottom'
                },
                command: 'save-block'
            });*/
        }

        model.set('toolbar', tb);
        model.presave = true;

    })

}
