import devices from './devices';
import extendAssetsModal from './assets';
import extendBlockManager from './BlockManager';
import extendCodeEditor from './CodeEditor';
import extendCommand from './command';
import parser from './parser';

export default (editor, config) => {
	let defaults = {
		// Grapick options
		grapickOpts: {min: 1,max: 99},

		// Custom color picker, check Grapick's repo to get more about it
		// If you leave it empty the native color picker will be used.
		// You can use 'default' string to get the one used by Grapesjs (which
		// is spectrum at the moment of writing)
		colorPicker: 'default',

		// Show gradient direction input under picker, you can pass an object
		// as a model
		inputDirection: 1,

		// Show gradient type input under picker, you can pass an object as
		// a model
		inputType: 1,
	};

	// Load defaults
	const config_gradient = { ...defaults, ...config };
	devices(editor, config);
	extendAssetsModal(editor, config);
	extendBlockManager(editor, config);
	extendCodeEditor(editor, config);
	// extendGradient(editor, config_gradient);
	extendCommand(editor, config);
	editor.setCustomParserCss(parser);
}
