import { isArray } from 'underscore';

export default (editor, options = {}) => {
    const bm = editor.BlockManager,
        blocks = editor.editor.get('t4-config').blocks;
    const { opts } = options;
    // add default block
    bm.add('Alert', {
        label: 'Alert',
        category: 'Basic',
        attributes: {class:'fal fa-exclamation-triangle'},
        group: 'Element',
        content: {
            type: 't4.alert',
            content: 'Sample Alert'
        }
    })
  
// add default blank section
    bm.add('heading', {
        label: 'Heading',
        category: 'Basic',
        attributes: {class:'fal fa-heading'},
        group: 'Element',
        content: {
            type: 'text',
            tagName: 'h1',
            content: 'Heading Title'
        }
    });

    // add default blank section
    bm.add('text', {
        label: 'Text',
        category: 'Basic',
        attributes: {class:'fal fa-font'},
        group: 'Element',
        content: {
            type: 'text',
            content: 'Vivamus sagittis lacus vel augue laoreet rutrum faucibus dolor auctor. Duis mollis, est non commodo luctus.'
        }
    });
    
    // add default block
    bm.add('Link', {
        label: 'Link',
        category: 'Basic',
        attributes: {
            id:'',
            class:'fal fa-link'
        },
        group: 'Element',
        content: '<a style="padding:5px;" href="#" class="t4b-link" t4-type="link" title="Sample link"><span>Link</span></a>',
    })
    // add default block
    bm.add('Box', {
        label: 'Box',
        category: 'Basic',
        attributes: {
            id:'',
            class:'fal fa-square'
        },
        group: 'Element',
        content: '<div style="padding: 8px; margin-bottom: 8px; min-height: 50px;" class="t4b-box"></div>',
    })

    if (blocks) {
        blocks.forEach(block => {

            var b = {...block},
                id = block.id,
                attrs = `t4-bid="${id}" t4-group="${block.group}"`;
            b.content = block.content.replace(/^<([^>]+)>/, '<$1 ' + attrs + '>');

            /*
            if (block.group == 'Block' || block.group == 'Element') {
                const content = block.content.replace(/^<([^>]+)>/, '<$1 t4-bid="' + id + '">');
                b.content = {
                    type: block.group == 'Block' ? "section" : 'block',
                    content
                }
            }
            */
            if(b.thumbUrl){
                var thumb = '<img src="'+b.thumbUrl+'" alt="'+b.label+'" title="'+b.label+'">';
                b.label = '<div class="gjs-block-thumbnail">' + thumb + '</div>\n<div class="gjs-block-text" title="' + block.label + '"></div>';
            } else if(b.thumb){
                var thumb = '<img src="data:image/png;base64,'+b.thumb+'" alt="'+b.label+'" title="'+b.label+'">';
                b.label = '<div class="gjs-block-thumbnail">' + thumb + '</div>\n<div class="gjs-block-text" title="' + block.label + '"></div>';

            } else {
                b.label = (b.icon ? b.icon + '\n' : '') + '<div class="gjs-block-label">' + b.label + '</div>';
            }
            bm.add(id, b);

            // parse model for using it later on
            if (block.model) {
                try {
                    block.model = eval('(' + block.model + ')')
                } catch (e) {console.log('err: ', e, block)}
            }

        })
    }

    // console.log
    // add class when component added
    editor.on('component:add', model => {
        if (model.get('type').match(/.+_.+_.+/) && model.opt.at) model.addClass(model.cid);
    });
    
    editor.on('block:drag:stop', (model, block) => {
        if (!model) return;
        // droppable
        //model.addAttributes({'t4-bid': block.id});
        // update child block trait
        // if(model.find('block').length) model.find('block').forEach(block => block.addBlockTraits());
    });
}
