import jdocModel from './jdoc';
export default (editor) => {
    const jdoc = jdocModel(editor);
    const em = editor.editor;

    return jdoc.extend({
        defaults: {
            ...jdoc.prototype.defaults,
            name: 'Positions',
            type: 'modules',
            traits: [{
                type: 'select',
                label: 'Position',
                name: 'name',
                options: em.get('t4-config').positions.slice(0)
            }, {
                type: 'select',
                label: 'Module Style',
                name: 'modulestyle',
                changeProp: 1,
                options: em.get('t4-config').modulestyles.slice(0)
            }, {
                type: 'select',
                label: 'Avaibility',
                name: 'avaibility',
                options: ['all', 'parent', 'child']
            }],
            attributes: {
                type: 'modules',
                name: "pos-1"
            }
        },
        init() {
            if (this.opt.temporary) return;
            this.initAttr();
            this.listenTo(this, 'change:modulestyle', this.updateModulestyle);
        },

        initAttr(){
            var attr = this.getAttributes();
            this.set('name',attr.name);
            this.set('modulestyle',attr.modulestyle);
        },
        updateModulestyle() {
            this.view.$el.attr('modulestyle', this.get('modulestyle'));
        },
        getAttrToHTML() {
            var attr = this.getAttributes();
            attr.modulestyle = this.get('modulestyle');
            return attr;
        },
        toHTML() {
            this.updateModulestyle();
            var code = jdoc.prototype.toHTML.apply(this);
            return code;
        }
    }, {
        isComponent(el) {
            return el.tagName == 'JDOC:INCLUDE' && el.getAttribute && el.getAttribute('type') && el.getAttribute('type').toLowerCase() == 'modules';
        },
    })
}
