export default (editor, config = {}) => {
    const dc = editor.DomComponents;
    const defaultType = dc.getType("default");
    const textType = dc.getType("text");
    dc.addType('default', {
        model: {
            defaults: {
                ...defaultType.model.prototype.defaults,
                traits: [
                    ...defaultType.model.prototype.defaults.traits,
                    {
                        label: "Animation",
                        type: "animation",
                        name: "animation"
                    }
                ]
            }
        }
    });
    dc.addType('text', {
        model: {
            defaults: {
                ...textType.model.prototype.defaults,
                traits: [
                    ...textType.model.prototype.defaults.traits,
                    {
                        label: "Animation",
                        type: "animation",
                        name: "animation"
                    }
                ]
            }
        }
    });
};