export default (editor) => {

  var Component = editor.DomComponents.Component;

  return Component.extend({
    defaults: {
      ...Component.prototype.defaults,
      type: 'jdoc',
      tagName: 'jdoc:include',
      droppable: 0,
      editable: 0,
      highlightable: 0,
      stylable: 0,
      resizable: 0,
      traits: [],
      attributes: {
        type: 't4'
      }
    },
    
    init() {
    },
  }, {
    isComponent(el) {
      return el.tagName == 'JDOC:INCLUDE';
    },
  })
}