/**
 * You can customize the initial state of the module from the editor initialization, by passing the following [Configuration Object](https://github.com/artf/grapesjs/blob/master/src/commands/config/config.js)
 * ```js
 * const editor = grapesjs.init({
 *  commands: {
 *    // options
 *  }
 * })
 * ```
 *
 * Once the editor is instantiated you can use its API. Before using these methods you should get the module from the instance
 *
 * ```js
 * const commands = editor.Commands;
 * ```
 *
 * * [add](#add)
 * * [get](#get)
 * * [getAll](#getall)
 * * [has](#has)
 * * [run](#run)
 * * [stop](#stop)
 * * [isActive](#isactive)
 * * [getActive](#getactive)
 *
 * @module Commands
 */

import { isFunction, isUndefined } from 'underscore';

module.exports = () => {
    let em;
    var c = {},
        commands = {},
        revisionCommand = {},
        defaults = require('./config/config');
    const active = {};

    return {

        /**
         * Name of the module
         * @type {String}
         * @private
         */
        name: defaults.label,
        /**
         * revDel of the module
         * @type {initerger}
         * @private
         */
        revDelConfig: defaults.revDel,
        /**
         * Initialize module. Automatically called with a new instance of the editor
         * @param {Object} config Configurations
         * @private
         */
        init(config) {
            c = config || {};
            for (var name in defaults) {
                if (!(name in c)) c[name] = defaults[name];
            }
            em = c.em;
            // this.set('revDel',revDel);
            var ppfx = c.pStylePrefix;
            em.getEditor().revisionManager = this;
            if (ppfx) c.stylePrefix = ppfx + c.stylePrefix;
            revisionCommand = require('./view/RevisionsView');
            em.getEditor().Commands.add('open-revision', revisionCommand);

            em.on('load', (editor) => {
                this.revDelConfig = em.config.revisionManager.revDel;
                if (this.revDelConfig) revisionCommand.revDelConfig = editor.revisionManager.revDelConfig;
            });

            em.getEditor().Panels.addButton('views', {
                id: 'open-revision',
                label: '<span>Revisions</span>',
                className: 'far fa-history',
                command: 'open-revision',
                attributes: {
                    title: '',
                    'data-tooltip-pos': 'right',
                    'data-tooltip': 'Revision Manager'
                },
                active: false,
            });

            return this;
        }/*,
        revDelEl(editor, id) {
            let btnDel = document.createElement('button');
            btnDel.setAttribute('class', 'gjs-revision-del');
            btnDel.innerHTML = 'x';
            btnDel.addEventListener('click', function() {
                let alerts = confirm('Are you sure?');
                if (alerts == true) {
                    console.log(id);
                    revisionCommand.revDel(editor, id);
                } else {
                    return false;
                }
            });
            return btnDel;
        }*/
    };
};
