import _ from 'underscore';
import Backbone from 'backbone';

module.exports = Backbone.View.extend({
  template: _.template(`
    <label><%= label %></label>
    <div class="<%= ppfx %>field <%= ppfx %>select">
      <span id="<%= ppfx %>input-holder">
        <select class="<%= ppfx %>styles"></select>
      </span>
      <div class="<%= ppfx %>sel-arrow">
        <div class="<%= ppfx %>d-s-arrow"></div>
      </div>
    </div>
  `),

  events: {
    change: 'updateStyle'
  },

  initialize(o) {
    this.config = o.config || {};
    this.em = this.config.em;
    this.ppfx = this.config.pStylePrefix || '';
    this.listenTo(this.em, 'change:templatestyle', this.updateSelect);
    this.delegateEvents();
  },

  /**
   * Update device of the editor
   * @private
   */
  updateStyle() {
    var em = this.em;
    if (em) {
      var stylesEl = this.stylesEl;
      var val = stylesEl ? stylesEl.val() : '';
      em.set('templatestyle', val);
    }
  },

  /**
   * Update select value on device update
   * @private
   */
  updateSelect() {
    var em = this.em;
    var stylesEl = this.stylesEl;
    stylesEl.val(em.get('templatestyle'));
  },

  /**
   * Return styles options
   * @return {string} String of options
   * @private
   */
  getOptions() {
    var result = '<option value="">-- Select templatestyle --</option>';
    this.collection.each(style => {
      result += '<option value="' + style.get('id') + '">' + style.get('name') + '</option>';
    });
    return result;
  },

  render() {
    var pfx = this.ppfx;
    this.$el.html(
      this.template({
        ppfx: pfx,
        label: this.config.label
      })
    );
    this.stylesEl = this.$el.find('.' + pfx + 'styles');
    this.stylesEl.append(this.getOptions());
    this.el.className = pfx + 'styles-c';

    this.updateSelect();

    return this;
  }
});
