export default {
	model: {
		defaults: {
			name:"Link",
            smgroup: 'text',
			draggable: true,
			droppable: true,
			highlightable: true,
			editable: true,
			traits:[
				"title",
				"href",
				"target",
				{
					type:"animation",
					name:"Animation"
				}
			]
		},
		init(){
			this.em.getEditor().DomComponents.getType('link').model.prototype.init.apply(this, arguments);
		},
	},

	isComponent: el => {

		return (el.className && /(^|\s)(t4b-link)(\s|$)/.test(el.className)) && el.tagName && (el.tagName == "A");
	}
}