export default (editor, config = {}) => {
    const em = editor.editor;
    const dc = editor.DomComponents;
    const defaultType = dc.getType("text");
    const defaultModel = defaultType.model;
    const defaultView = defaultType.view;

    defaultModel.prototype.defaults = {
        ...defaultModel.prototype.defaults,
        smgroup: 'text'
    }

    defaultModel.prototype._init = defaultModel.prototype.init;

    defaultModel.prototype.init = function() {
        defaultModel.prototype._init.apply(this, arguments);

        const curTag = this.get("tagName");
        const tags = ["h1", "h2", "h3", "h4", "h5", "h6", "span", "p", "div"];
        if (!tags.includes(curTag)) return;

        // update tag option for current tagName
        var tagTrait = this.get("traits").find(el => el.get("name") == "tag");
        if (!tagTrait) {
            // add tag trait
            tagTrait = this.get("traits").add({
                type: "select",
                label: "Tag",
                name: "tag",
                options: tags,
                changeProp: 1
            });
        }
        this.set("tag", curTag);

        this.listenTo(this, "change:tag", (el, val) => {
            this.set("tagName", val);
        });
    };
};
