import Element from './element';
import {parseOverlay, buildOverlay} from './helper/overlay';

export default {
	extend: 't4.element',
	extendFn: ['init'],
	model: {
        defaults: {
        	container: '',
            traits: [
            'id',
            'title',
            
            {
                type: 'select',
                label: 'Tag',
                name: 'tagName',
                changeProp: 1,
                options: ['section', 'div', 'header', 'footer', 'nav', 'article']
            }, {
                type: 'select',
                label: 'Container Type',
                name: 'container',
                changeProp: 1,
                options: [
                    { value: '', name: 'No Container' },
                    { value: 'container', name: 'Default Container' },
                    { value: 'container-fluid', name: 'Fluid Container' },
                    // { value: 'container-sm', text: 'Small Container' },
                    // { value: 'container-md', text: 'Medium Container' },
                    // { value: 'container-lg', text: 'Large Container' }
                ]
            }/*, {
                type: 'checkbox',
                label: 'Heading',
                name: 'heading',
                changeProp: 1
            }*/,{
                label: "Animation",
                type: "animation",
                name: "animation"
            }],
            classes: ['t4b-section'],
            droppable: false,
            draggable: '#wrapper',
            smgroup: 'section',

        },

        init() {
        	if (this.opt.temporary) return;
        	this.detectContainer();
            this.detectHeading();
        	this.listenTo(this, 'change:container', this.updateContainer)
            // this.listenTo(this, 'change:heading', this.updateHeading)
        },

        setOverlay(value) {
        	this.set('overlay', value);
        	buildOverlay(this);
        },

        getOverlay() {
        	let overlay = this.get('overlay');
        	if (!overlay) {
        		overlay = parseOverlay(this);
        		this.set('overlay', overlay);        		
        	}

        	return overlay;
        },

        updateContainer() {
        	const contype = this.get('container'),
        		cls = contype ? contype : 'container-none';
        	const con = this.getContainer();
        	if (con) {
        		con.removeClass('container container-none container-fluid');
        		con.addClass(cls);
                return con;
        	} else {
        		const coms = this.components(),
        			child = coms.filter(c => c.get('type') != 't4.ghost' && !c.getClasses().includes('t4b-section__heading'));

                // only one child and is div box => make it as container
                if (child.length == 1 && child[0].get('tagName') == 'div') {
                    child[0].addClass(cls);
                    return child[0];
                }

        		coms.remove(child);
        		const con = coms.add({classes: cls});        		
        		con.components().add(child);
                return con;
        	}
        },

        detectContainer() {
        	const con = this.getContainer();
        	if (con) {
        		const cl = con.getClasses();
        		const contype = cl.includes('container-fluid') ? 'container-fluid' : (cl.includes('container') ? 'container' : '');
	        	this.set('container', contype);
        	}
        },

        getContainer() {
        	return this.components().find(c => {
        		return /(^|\s)container(-|\s|$)/.test(c.getClasses().join(' '))
        	});
        },

        detectHeading() {
            const model = this.getHeading();
            if (model) {
                this.set('heading', true);
            } else {
                this.set('heading', false);
            }
        },

        getHeading() {
            return this.components().find(c => c.getClasses().includes('t4b-section__heading'));
        },

        updateHeading() {
            const model = this.getHeading(),
                hasHeading = this.get('heading');
            if (hasHeading) {
                if (!model) {
                    const con = this.updateContainer(),
                        opt = {};

                    if (con) {
                        opt.at = con.collection.indexOf(con);
                    }

                    const added = this.components().add(
                        `<div class="t4b-section__heading text-center" t4-container>
                            <h2 class="t4b-section__title">Section title</h2>
                            <p class="t4b-section__desc">Section description text</p>
                        </div>`, opt);
                    added.set({
                        selectable: false,
                        droppable: true,
                        draggable: true
                    })
                }
            } else {
                if (model) model.remove();
            }

        }

	},

}
