const allitems = {};
export default {
	extend: 't4.element',

	isComponent: el => {
		if (!el.className) return false;
		return el.classList.contains('t4b-accordion')
	},

	model: {
		defaults: {
			droppable: false,
			editable: false,
			smgroup: 'accordion',
			//'list-type': '',
			itemname: 'Accordion Item',
			name: 'Accordion',
			unstylable:[
				'opacity',
			]
		},

		init() {
			if (this.opt.temporary) return;
			// init list
			this.updateChild(this)

			//this.listenTo(this, 'component:cloned', this.initList)
		},


		getListCarousel() {
			const items = {};
			const inner = this.find('.carousel-inner'),
				handler = this.find('.carousel-indicators')
			if (!handler.length || !inner.length) return items;
			inner[0].components().each((c, i) => {
				const item = {content: c, handler: handler[0].components().at(i)},
					id = item.content.getId();
				if (item.handler) item.handler.addAttributes({'data-target': '#' + id});
				item.content.addAttributes({'data-parent': '#' + this.getId()})
			})
			
			return items;
		},

		getList() {
			const items = {};

			this.components().each(c => {

				const handler = c.find('[data-toggle="collapse"]'),
					content = c.find('.collapse');
				if (!handler.length || !content.length) return items;

				const id = content[0].getId();				

				const item = {
					handler: handler[0],
					content: content[0]
				}

				item.handler.set({
					copyable: false,
					removable: false,
					draggable: false,
					toolbar: []
				})

				item.handler.addAttributes({'data-target': '#' + id});
				item.content.addAttributes({'data-parent': '#' + this.getId()})
				item.content.setId(id);

				c.set({
					selectable: true,
					draggable: false,
					smgroup: 'accordion',
					unstylable:[
						'opacity',
					],
					name: this.get('itemname')
				})

				items[id] = item;
			})

			return items;
		},

		getItem(id) {
			const items = allitems[this.getId()];
			return id === undefined ? items : items[id];
		},

		setActive(id, noSelectItem) {
			if (this.get('active-item') === id) return; 

			for(var _id in this.getItem()) {
				const item = this.getItem(_id);
				if (_id == id) {
					item.handler.view.$el.addClass('active');
					item.content.view.$el.addClass('active show');
				} else {
					item.handler.view.$el.removeClass('active');
					item.content.view.$el.removeClass('active show');
				}
			}

			if (!noSelectItem) {
				const item = this.getContentItem(id);
				if (item.view) setTimeout(() => {item.view.$el.trigger('click')}, 50)
			}

			this.set('active-item', id);
			
		},


		getContentCollection() {
			return this.components();
		},

		getContentItem(id) {
			return this.getItem(id).content.parent();
		},

		initList() {
			if (this.listInited) return;

			this.setId(this.getId());

			allitems[this.getId()] = this.getList();

			this.listenTo(this.getContentCollection(), 'add', this.cloneItem)
			this.listenTo(this.getContentCollection(), 'remove', this.removeItem)
			this.listenTo(this.getContentCollection(), 'list:moved', this.movedItem)

			this.getContentCollection().parent.set('sortable', 1);
	
			this.listInited = true;
		},

		removeItem(model, coll, opt) {
			if (opt.temporary) return;

			const items = allitems[this.getId()];
			for (var id in items) {
				if (!this.find('#' + id).length) {
					delete items[id];
				}
			}

			// get last item
			const coms = this.getContentCollection();
			if (coms.length) {
				this.setActive(coms.at(0).find('.collapse')[0].getId())
			} else {
				this.remove();
			}			
		},

		cloneItem(model, coll, opt) {
			if (opt.temporary) return;
			// clone handler and put item into list
			const items = this.getItem(),
				handler = model.find('[data-toggle="collapse"]'),
				content = model.find('.collapse'),
				newItem = {};

			newItem.item = model;
			newItem.handler = handler.length ? handler[0] : null;
			newItem.content = content.length ? content[0] : null;

			const id = newItem.content ? newItem.content.getId() : '';


			newItem.handler.addAttributes({'data-target': '#' + id});
			newItem.content.addAttributes({'data-parent': '#' + this.getId()});
			newItem.content.setId(id);

			items[id] = newItem;

			this.setActive(id);
		},

		movedItem(model, coll, opt) {
			this.view.render();
			this.setActive(model.find('.collapse')[0].getId());
		}

	},

	view: {
		events: {
			'click [data-toggle="collapse"]': 'toggle',
		},

		toggle(e) {
			if(!e.target.getAttribute('data-target')) return;
			const id = e.target.getAttribute('data-target').substr(1);
			this.model.setActive(id);
		},

		onRender() {
            if (this.model.opt.temporary) return;
            this.model.initList();
        }
	}
}