/**
 * You can customize the initial state of the module from the editor initialization, by passing the following [Configuration Object](https://github.com/artf/grapesjs/blob/master/src/device_manager/config/config.js)
 * ```js
 * const editor = grapesjs.init({
 *  deviceManager: {
 *    // options
 *  }
 * })
 * ```
 *
 * Once the editor is instantiated you can use its API. Before using these methods you should get the module from the instance
 *
 * ```js
 * const deviceManager = editor.DeviceManager;
 * ```
 *
 * * [add](#add)
 * * [get](#get)
 * * [getAll](#getAll)
 *
 * @module DeviceManager
 */

module.exports = () => {
    var c = {},
        defaults = require('./config/config'),
        Styles = require('./model/Styles'),
        StylesView = require('./view/StylesView'),
        em;
    var styles, view;

    return {
        /**
         * Name of the module
         * @type {String}
         * @private
         */
        name: 'TemplateManager',

        /**
         * Initialize module. Automatically called with a new instance of the editor
         * @param {Object} config Configurations
         * @param {Array<Object>} [config.styles=[]] Default styles
         * @example
         * ...
         * {
         *    styles: [
         *      {name: 'Desktop', width: ''}
         *      {name: 'Tablet', width: '991px'}
         *    ],
         * }
         * ...
         * @return {this}
         * @private
         */
        init(config) {
            c = config || {};
            em = c.em;
            if (c.template) return;

            c.styles = em.get('t4-config').templatestyles || [];
            c.templateDefault = c.templatestyle || '';
            for (var name in defaults) {
                if (!(name in c)) c[name] = defaults[name];
            }

            styles = new Styles(c.styles);
            view = new StylesView({
                collection: styles,
                config: c
            });

            em.on('change:templatestyle', this.applyStyle);

            /*em.on('load', () => {
                em.get('Panels').getPanel('devices-c').set('appendContent', this.render());
            })*/

            em.on('state:load', () => {
                em.get('Panels').getPanel('devices-c').set('appendContent', this.render());
                const templatestyle = em.get('StateManager').get('templatestyle') || c.templateDefault;
                em.set('templatestyle', templatestyle, {silent: 1});
                //this.applyStyle();
                if(templatestyle) em.trigger('change:templatestyle');
            })

            return this;
        },

        /**
         * Return all styles
         * @return {Collection}
         * @example
         * var styles = deviceManager.getAll();
         * console.log(JSON.stringify(styles));
         * // [{name: 'Desktop', width: ''}, ...]
         */
        getAll() {
            return styles;
        },

        reset(styles) {
            view.collection.reset(styles);
            view.render();
        },

        /**
         * Render styles
         * @return {string} HTML string
         * @private
         */
        render() {
            if (c.template) return '';
            return view.render().el;
        },

        applyStyle() {
            // remove all current template style url
            const doc = em.get('Canvas').getDocument();
            const head = doc.querySelector('head');
            const currentStyles = head.querySelectorAll('[templatestyle]');
            currentStyles.forEach(s => s.parentNode.removeChild(s));

            const styleid = em.get('templatestyle');
            em.get('StateManager').set('templatestyle', styleid);
            let url = em.get('ajaxUrl').buildAjaxUrl('head', {styleid});
            fetch(url)
                .then(response => {
                    return response.json();
                }).then(urls => {
                    // apply new style
                    if (!urls) return;
                    for (var url in urls) {
                        let link = doc.createElement('link');
                        link.rel = 'stylesheet';
                        link.type = 'text/css';
                        link.href = url;
                        link.setAttribute('templatestyle', '');
                        head.appendChild(link);
                    }
                })

        },
    };
};
