export default (editor, config = {}) => {
  // override renderBody to render base before 
  var canvasView = editor.Canvas.getCanvasView();
  var baseUrl = config.baseUrl;
  if (!baseUrl.match(/^http/)) baseUrl = location.protocol + '//' + location.host + baseUrl;

  canvasView._renderBody = canvasView.renderBody;
  canvasView.renderBody = () => {
    var doc = editor.Canvas.getDocument(),
    	base = doc.createElement('base');
    base.href = baseUrl;
    doc.head.appendChild(base);

    canvasView._renderBody();
  }

  // add base for editor
  var base = document.querySelector('base');
  if (!base) {
  	base = document.createElement('base');
  	document.head.appendChild(base);
  }
  base.href = baseUrl;


  // canvas scroll
  editor.on('canvasScroll', () => {
    var body = editor.Canvas.getFrameEl().contentDocument.body,
      top = body.scrollTop,
      cmdPanel = editor.Panels.getPanel('commands');
    if (top) {
      cmdPanel.view.$el.addClass('canvas-is-scrolled');
    } else {
      cmdPanel.view.$el.removeClass('canvas-is-scrolled');
    }
  })
}
