export default (editor, options = {}) => {
	// temporary fix for command 
	editor.Canvas.isInputFocused = () => false;

	// moving event

	let dragPos, dropPos, srcModel, srcColl, dstColl;
    editor.on('component:dragEnd:before', (dst, srcEl, pos) => {
    	const $ = editor.$;
    	// console.log('bf: ', dst, srcEl, pos);
        srcModel = srcEl ? $(srcEl).data('model') : null;
        srcColl = srcModel ? srcModel.collection : null;
        dstColl = $(dst).data('collection');
        dropPos = pos ? (pos.method === 'after' && srcColl != dstColl ? pos.index + 1 : pos.index) : 0;
        dragPos = srcColl ? srcColl.findIndex(srcModel) : -1;
    })

    editor.on('component:dragEnd', (collection, model, warns) => {	
    	if (!collection) return;
    	const created = collection.at(dropPos);
    	if (created) {
	    	created.trigger('moved', {
	    		dragPos,
	    		dropPos,
	    		srcModel,
	    		srcColl,
	    		dstColl
	    	})
	    }
    })
/*
    editor.on('canvas:dragover',(index) => {
        var space = document.createElement('div');
        space.setAttribute("class", "drag-space");
        var target = index.target;
        var layerY = index.layerY;
        target.append(space);
        console.log(layerY)
        console.log(target)
        console.log(space)
    });*/
    // debug
    // editor.on('storage:start:store', () => {
    //     console.log(new Error().stack);
    //     return false;        
    // })
}