import extendText from './text';
import animation from './animation';
import typed from './typed';
import SelectGroup from './selectgroup';
//import sectionComp from './section/index';
//import t4Comp from './t4comp/index';
import t4Comps from './comps/index';
import sliders from './sliders/index';
//import countdown from './countdown/index';

export default (editor, config = {}) => {
    extendText(editor, config);
    animation(editor, config);
    
    SelectGroup(editor, config);
    //sectionComp(editor, config);
    //t4Comp(editor, config);
    t4Comps(editor, config);
    sliders(editor, config);
    // countdown(editor, config);
    //config typed
    const options = { ...{
    script: '',
        // Object to extend the default block, eg. `{ label: 'Typed', ... }`
        // Pass a falsy value to avoid adding the block
        block: {},

        // Customize the component props. The final object should be returned
        // from the function.
        /**
         eg. Here an example of how you would customize component's traits
         `props => {
            props.traits = props.traits.map(trait => {
              if (trait.name == 'strings') {
                trait.label = 'Custom <b>trait<b/> label';
              }
              // this trait will be removed
              if (trait.name == 'fade-out-class') return;
              return trait;
            }).filter(i => i);

            return props;
         }`
         */
        props: i => i,
    },  ...config };
    
    typed(editor,options);

    // overwrite highlightable & hoverable of all
    editor.DomComponents.getType('default').model.prototype.hasAttribute = function(name) {
        return this.getAttributes()[name] !== undefined }

    const resetHilite = model => {
        const val = model.get('selectable');
        const highlightable = model.get('highlightable') ? model.get('highlightable') : val;
         model.set({ highlightable: highlightable, hoverable: val });
    }

    editor.on('component:create', model => {
        // if (model.hasAttribute('t4-nosel')) model.set('selectable', false);
        // if (model.hasAttribute('t4-nostyle')) model.set('stylable', false);
        // if (model.hasAttribute('t4-nodrop')) model.set('droppable', false);
        // if (model.hasAttribute('t4-nodrag')) model.set('draggable', false);

        resetHilite(model);

        model.on('change:selectable', () => {resetHilite(model)})
    })

    editor.getWrapper().set({
        smgroup: 'body',
        stylable: 'true'

    });
    // editor.on('component:mount', model => {
    //     if (model.get('type') == 'block') {
    //         const childs = model.find('[t4b-type="block-child"]');
    //         childs.forEach(child => {
    //             child.set('block', model);
    //             var mapfields = child.getAttributes()['t4b-map'];
    //             console.log('xx: ', propfields, textfield)
    //         })
    //     }
    // })

}
