export default (editor, config = {}) => {
	let merge_array = function (...args){
	   let set = new Set(); // init Set object (available as of ES6)
	   for(let arr of args){ // for of loops through values
	      if(!Array.isArray(arr)){
	         console.error(`Array input expected "${typeof arr}" given. Invalid value:`, arr);
	         return 'error'
	      }else{
	         arr.map((value) => { // map adds each value to Set object
	            set.add(value); // set.add method adds only unique values
	         });
	      }
	   }
	   return [...set]; // destructuring set object back to array object
	   // alternativly we culd use:  return Array.from(set);
	}

	const commands = editor.Commands;
	commands.add('tlb-move-prev', editor => {
	  const model = editor.getSelected(),
	  	coll = model.collection,
	  	pos = coll.indexOf(model),
	  	silent = {silent: true};
	  if (pos < 1) return;
	  coll.remove(model, silent);
	  coll.add(model, {at: pos-1, ...silent});
	  coll.trigger('list:moved', model, coll, {at: pos, to: pos-1});
	  // reselect
	  editor.DomComponents.selectAdd(model);
	});

	commands.add('tlb-move-next', editor => {
	  const model = editor.getSelected(),
	  	coll = model.collection,
	  	pos = coll.indexOf(model),
	  	silent = {silent: true};
	  if (pos >= coll.length-1) return;
	  coll.remove(model, silent);
	  coll.add(model, {at: pos+1, ...silent})
	  coll.trigger('list:moved', model, coll, {at: pos, to: pos+1});
	  // render

	  // reselect
	  editor.DomComponents.selectAdd(model)
	});


	editor.on('component:selected', (model) => {
		//change delete button to last item on selected
		if (!model || !model.parent()) return;
		
		let tb = model.get('toolbar');
		if(tb == '' || tb.length == '0' || typeof tb == 'undefined') return;
		var savebtn = tb.find(cm => cm.name == 'save-btn');
		var shareEdit = tb.find(cm => cm.attributes.class == 'far fa-edit');
		let blockEdtior = tb.find(cm => cm.command == 'block-editor');
		// let customCode = tb.find(cm => cm.command == 'custom-code:open-modal');
		// var viewOffcanvas = tb.find(cm => cm.command == 'tlb-offcanvas');
		let ppfx = editor.editor.getConfig('stylePrefix');
		let tbnew = [];

		// Move next/prev for list item
		if (model.parent() && model.parent().get('sortable') && model.collection.indexOf(model) > 0) {
			tbnew.push({
				    attributes: { 
				    	class: 'fal fa-arrow-left',
				    	title: '',
				    	'data-tooltip': 'Move Prev',
				    	'data-tooltip-pos': 'bottom'
				     },
				    command: 'tlb-move-prev'
			  	});		  	
		}
		if (model.parent() && model.parent().get('sortable') && model.collection.indexOf(model) < model.collection.length-1) {
			tbnew.push({
				    attributes: { 
				    	class: 'fal fa-arrow-right',
				    	title: '',
				    	'data-tooltip': 'Move Next',
				    	'data-tooltip-pos': 'bottom'
				     },
				    command: 'tlb-move-next'
			  	});		  	
		}
		// \End move prev/next

		if (model.collection && model.parent().getName() != 'Body') {
			tbnew.push({
				    attributes: { 
				    	class: 'fal fa-arrow-up',
				    	title: '',
				    	'data-tooltip': 'Select Parent',
				    	'data-tooltip-pos': 'bottom'
				     },
				    command: 'core:component-exit'
			  	});
		  	
		}
		if (model.get('draggable') && (model.getName() != "T4.container")) {
		  	tbnew.push({
		    	attributes: {
			      	class: `fal fa-arrows ${ppfx}no-touch-actions`,
			      	draggable: true,
			      	title: '',
		    		'data-tooltip': 'Move the section',
			    	'data-tooltip-pos': 'bottom'
			    },
			    command: 'tlb-move'
			});
			
		}
		if (model.get('copyable')) {
		  tbnew.push({
		    attributes: { 
		    	class: 'fal fa-clone',
		    	title: '',
		    	'data-tooltip': 'Clone the section',
		    	'data-tooltip-pos': 'bottom'
			 },
		    command: 'tlb-clone'
		  });
		}
	  	
		if(blockEdtior){
			tbnew.push(blockEdtior);
		}
		savebtn && tbnew.push(savebtn);
		// customCode && tbnew.push(customCode);
		shareEdit && tbnew.push(shareEdit);
		// viewOffcanvas && tbnew.push(viewOffcanvas);
		//merge toolbar all component
		let tbnews = [...tbnew, ...tb];
		if (model.get('removable')) {
		 	// tbnews.splice(tbnews.findIndex(btn => btn.attributes.class.includes('fa-trash-alt')), 1);
		  	tbnew.push({
			    attributes: { 
			    	class: 'fal fa-trash-alt',
			    	title: '',
			    	'data-tooltip': 'Remove Selected',
			    	'data-tooltip-pos': 'bottom'
				},
			    command: 'tlb-delete'
		  	});
		}
		model.set('toolbar',tbnew);
	});
}