//var AssetDir = require('./AssetDir');

export default (editor, options = {}) => {
    let config = editor.getConfig();
    let pfx = config.stylePrefix || '';
    const am = editor.AssetManager;
    const amv = am.AssetsView();
    let btnMediaRendered = false;
    const $ = editor.$;

    // When the Asset Manager modal is opened
    editor.on('run:open-assets', () => {
        if (btnMediaRendered) return;

        const modal = editor.Modal;
        const modalBody = modal.getContentEl();
        const uploader = modalBody.querySelector('.' + pfx + 'am-file-uploader');
        const assetsBody = modalBody.querySelector('.' + pfx + 'am-assets-cont');
        const assetsHeader = modalBody.querySelector('.' + pfx + 'am-assets-header');

        uploader && (uploader.style.display = 'none');
        assetsBody.style.width = '100%';

        // Instance button if not yet exists
        let btnEl = document.createElement('span');
        btnEl.className = pfx + 'btn-media far fa-image';
        btnEl.innerHTML = 'Media';

        let frontEdit = localStorage.getItem('T4EDIT_VISITED');
        if(frontEdit == 'true'){
            $(assetsHeader).addClass('hidden-btn-media');
            btnEl.style.display = "none";
        }else{

            btnEl.onclick = () => {
                
                $(document).off('t4:media-selected').one('t4:media-selected', (e) => {
                    let imgUrl =  e.selectedUrl;
                    am.add(imgUrl);
                    editor.getSelected().set('src', imgUrl);
                    modal.close();
                });
                const ev = new CustomEvent('t4:media-open');
                document.dispatchEvent(ev);
            };
        }

        
        assetsHeader.appendChild(btnEl);
        btnMediaRendered = true;

    });

}
