import animationTrait from "./animationTrait";
import typedTraits from "./typedTraits";
export default (editor, config) => {
    const tm = editor.TraitManager;

    tm.addType("media", {
        events: {
            "click .btn": "handleClick"
        },

        templateInput: "",

        handleClick() {
            const e = new CustomEvent('t4:media-open');
            // e.inputUrl = inputAssetUrl;
            document.dispatchEvent(e);
            editor.$(document).off('t4:media-selected').one('t4:media-selected', (e) => {
                this.setInputValue(e.selectedUrl);
            });
        },

        getInputEl() {
            if (!this.input) {
                const model = this.model;
                const value = this.getModelValue();

                this.$input = $(`<span><input type="text" /><span class="btn btn-default"><i class="far fa-image"></i></span></span>`);

                this.input = this.$input.find('input').attr('value', value).get(0);
            }

            return this.$input.get(0);
        },

        setInputValue(value) {
            const el = this.getInputElem();
            el && (el.value = value);
            if (value != this.getModelValue()) {
                this.model.setTargetValue(value);
            }
        },
    });
    animationTrait(editor, config);
    // typedTraits(editor, config);
};
