import _ from 'underscore';
let settingData,modalSettings;
module.exports = {
	updateOptionFont(editor,data){
		const fontProperty = editor.StyleManager.getProperty('typography', 'font-family');
        let fontArr = Object.values(editor.SettingManager.fontdefaults);
        var ss = ', sans-serif';
      	var fonts = [
            'Arial, Helvetica' + ss,
            'Arial Black, Gadget' + ss,
            'Brush Script MT' + ss,
            'Comic Sans MS, cursive' + ss,
            'Courier New, Courier, monospace',
            'Georgia, serif',
            'Helvetica, serif',
            'Impact, Charcoal' + ss,
            'Lucida Sans Unicode, Lucida Grande' + ss,
            'Tahoma, Geneva' + ss,
            'Times New Roman, Times, serif',
            'Trebuchet MS, Helvetica' + ss,
            'Verdana, Geneva' + ss
      	];
  		let list = [];
        for (var j = 0, l = fonts.length; j < l; j++) {
            var font = {};
            font.value = fonts[j];
            font.name = fonts[j].split(',')[0];
            list.push(font);
        }
        fontArr.forEach(function(font){
	      	list.push({value:font.name, name: font.name});
	    });
        fontProperty.set('list', list);
		fontProperty.view.updateOptions();
		fontProperty.setValue(fontProperty.getFullValue());
		return this;
	},
    updateSettings(editor,data){
      const url = this.em.get('ajaxUrl').buildAjaxUrl('updategooglefont');
      fetch(url, {
            method: "POST",
             headers: {
                "Content-Type": "application/json",
                // "Content-Type": "application/x-www-form-urlencoded",
            },
            body: JSON.stringify({ data })
  		}).then(res => {
            let restult = editor.SettingManager.setValue(data.dataFont);
            this.updateOptionFont(editor,data.dataFont);
            this.updateHead(data.dataFont);
            editor.toastr.success('Settings has updated successfuly!');
        }).catch(err => {
            console.log(err);
            editor.toastr.error('Error!!!');
        });
    },
    setEventModal(editor,modalEl){
    	const $ = editor.$;
        let that = this;
    	let nameFont = $('.load-google-fonts').val();
        //update value form load font icon
        let dataFontIcons = $('#load_font_icons').val();
        let configAwesomeIcons = '',configMaterialIcons = '';
        if(dataFontIcons){
            dataFontIcons = JSON.parse(dataFontIcons);
            configAwesomeIcons = dataFontIcons.awesome_icons;
            configMaterialIcons = dataFontIcons.material_icons;
        }
        
        if(configAwesomeIcons){
            if(configAwesomeIcons.url_type == 'cdn'){
                $(document).find('.load-font-awesome_icons').find('.btn-cdn').addClass('active'); 
                $(document).find('.load-font-awesome_icons').find('.control-group.custom-url').hide();
            }else{
                $(document).find('.load-font-awesome_icons').find('.btn-url').addClass('active'); 
                $(document).find('.load-font-awesome_icons').find('.control-group.custom-url').show();
                $(document).find('.load-font-awesome_icons').find('#url_awesome_icons').val(configAwesomeIcons.custom_url);
            }
        }
        if(configMaterialIcons){
            if(configMaterialIcons.url_type == 'cdn'){
                $(document).find('.load-font-material_icons').find('.btn-cdn').addClass('active'); 
                $(document).find('.load-font-material_icons').find('.control-group.custom-url').hide();
            }else{
                $(document).find('.load-font-material_icons').find('.btn-url').addClass('active'); 
                $(document).find('.load-font-material_icons').find('.control-group.custom-url').show();
                $(document).find('.load-font-material_icons').find('#url_material_icons').val(configMaterialIcons.custom_url);
            }
        }
        
	    if(nameFont) {
	        let nameFonts = Object.values(JSON.parse(nameFont)),fontname = [];
	        nameFonts.forEach(function(font){
	            $(modalEl.querySelector('.t4-google-content')).find('li[data-name="'+font.name+'"]').not('.btn').addClass('font-active');
	        });
	        nameFonts.forEach(function(font){
		      	let fontval = font.name;
		      	if(font.weight.length) fontval += ":"+font.weight.join(',');
		      	fontname.push(fontval);
	      	});
	        this.updateHead(nameFont);
		    $('#t4-font-filter').val('');
	        $('.t4-font-filter').hide();
	    }
        let btns = modalEl.querySelectorAll('.nav-link');
        if(btns.length){
        btns.forEach(btn => {
          btn.onclick = function(e){
            e.preventDefault();
            that.tabActive(editor,btn);
          };
        })
      }
    },
    updateHead (data){
    	let nameFonts = Object.values(JSON.parse(data)),fontname = [];
        nameFonts.forEach(function(font){
	      	let fontval = font.name;
	      	if(font.weight.length) fontval += ":"+font.weight.join(',');
	      	fontname.push(fontval);
      	});
        $('head').find('.loadfonts').remove();
        $('<link class="loadfonts" href="https://fonts.googleapis.com/css?family='+escape(fontname.join('|'))+'" rel="stylesheet" type="text/css" />').appendTo($('head'));
    },
    tabActive(editor,target) {
        let $ = editor.$;
        let tab = $(target);
        let tabCurrentactive =  $(target).parents('ul').find('.active');
        let href = target.getAttribute('href');
        let currentHref = tabCurrentactive.get(0).getAttribute('href');
        let tabContent = $(href);
        if(href === currentHref) return;
        let currentTabAct = tabContent.parents('.t4b-tabs-content').find('.show.active');
        currentTabAct.removeClass('show active');
        currentTabAct.addClass('fade');
        tabContent.addClass('show active');
        tabContent.removeClass('fade')
        tab.addClass('active');
        tabCurrentactive.removeClass('active');

    },

    fetchSettingData(editor) {
      let that = this;
      const url = this.em.get('ajaxUrl').buildAjaxUrl('loadgooglefont');
      fetch(url)
        .then(res => res.json())
        .then(data => {
          settingData = data.data;
          this.settingRender(editor);
        }).catch(err => {
          console.log(err);
        })
    },
    updateView(editor,modalEl){
        let fontArr = Object.values(editor.SettingManager.fontdefaults);

    },
    settingRender(editor) {
    	if(!modalSettings) modalSettings = editor.Modal;
    	const $ = editor.$;
		let that = this;
	    let btnEdit = document.createElement('button');
	    let btnCancel = document.createElement('button');
	    btnEdit.setAttribute('type', 'button');
	    btnEdit.innerHTML = 'Update';
	    btnCancel.setAttribute('type', 'button');
	    btnCancel.setAttribute('class', 't4b-cancel');
	    btnEdit.setAttribute('class', 't4b-update');
	    btnCancel.innerHTML = 'Cancel';
	    const div = document.createElement('div');
	    div.setAttribute('class', 't4b-action');
	    div.appendChild(btnCancel);
	    div.appendChild(btnEdit);
        if(settingData){
            btnEdit.onclick = function() {
                let dataFont = $('.load-google-fonts').val();
                let dataFontIcons = $('#load_font_icons').val();
                let data = {dataFont,dataFontIcons};
                that.updateSettings(editor,data);
                settingData = '';
                modalSettings.close();
            }
            btnCancel.onclick = function() {
                modalSettings.close();
            }
            modalSettings.setContent(settingData);
            modalSettings.getContentEl().appendChild(div);
            // add Event
            this.setEventModal(editor,modalSettings.getContentEl());
            modalSettings.setTitle('Setting Managers');
            modalSettings
                    .open()
                    .getModel()
                    .once('change:open', () => editor.stopCommand(this.id));
        }else{
            this.fetchSettingData(editor);
        }
    },
    run(editor, sender) {
    	let openSMBtn = editor.Panels.getButton("views", "open-sm");
    	let openBlocksBtn = editor.Panels.getButton("views", "open-Element");
    	if(editor.getSelected()){
    		openSMBtn.set('active',true);
    	}else{
    		openBlocksBtn.set('active',true);
    	}
    	sender && sender.set('active',false);
    	this.settingRender(editor);
    }
}