import jdocModel from './jdoc';
export default (editor) => {
    const jdoc = jdocModel(editor);
    const em = editor.editor;
    const mod_object = em.get('t4-config').modules.slice(0);
    const mod_arr = [];
    for (var i = 0; i < mod_object.length; i++) {
        mod_arr.push({value : String(mod_object[i].id), name : mod_object[i].title});
    }

    return jdoc.extend({
        defaults: {
            ...jdoc.prototype.defaults,
            name: 'Module',
            type: 'module',
            traits: [{
                type: 'select',
                label: 'Module',
                name: 'modname',
                changeProp: 1,
                options: mod_arr
            }, {
                type: 'select',
                label: 'Module Style',
                name: 'modulestyle',
                changeProp: 1,
                options: em.get('t4-config').modulestyles.slice(0)
            }, {
                type: 'select',
                label: 'Avaibility',
                name: 'avaibility',
                options: ['all', 'parent', 'child']
            }],
            attributes: {
                type: 'module',
                name: ''
            }
        },
        init() {
            if (this.opt.temporary) return;
            this.initAttr();
            this.listenTo(this, 'change:modulestyle', this.updateModulestyle);
            this.listenTo(this, 'change:modname', this.updateModule);
        },
        updateModulestyle() {
            this.view.$el.attr('modulestyle', this.get('modulestyle'));
        },
        initAttr(){
          var attr = this.getAttributes();
          this.set('modname',attr.modid);
          this.set('modulestyle',attr.modulestyle);
        },
        updateModule() {
            var val = this.get('modname');
            var attr = this.getAttributes();
            for (var i = 0; i < mod_object.length; i++) {
                if (mod_object[i].id == val) {
                    attr.modid = val;
                    attr.title = mod_object[i].title;
                    attr.name = mod_object[i].module;
                }
            }
            this.setAttributes(attr);
        },
        getAttrToHTML() {
            var attr = this.getAttributes();
            attr.modulestyle = this.get('modulestyle');
            return attr;
        },
        toHTML() {
            this.updateModulestyle();
            this.updateModule();
            var code = jdoc.prototype.toHTML.apply(this);
            return code;
        }
    }, {
        isComponent(el) {
            return el.tagName == 'JDOC:INCLUDE' && el.getAttribute && el.getAttribute('type') && el.getAttribute('type').toLowerCase() == 'module';
        },
    })
}
