export default (editor, options = {}) => {

    var pfx = editor.getConfig().stylePrefix;
    var modal = editor.Modal;
    var cmdm = editor.Commands;
    var pnm = editor.Panels;
    var btnEdit = document.createElement('button');
    var btnCancel = document.createElement('button');
    btnEdit.setAttribute('type', 'button');
    btnCancel.setAttribute('type', 'button');
    class CodeViewer {
        constructor(editor, senderBtn, opts) {
            this.editor = editor;
            this.opts = opts || { inlineCss: false };
            this.senderBtn = senderBtn;
            this.isShowing = true;
        }
        setAttributes(el, attrs) {
            for (var key in attrs) {
                el.setAttribute(key, attrs[key]);
            }
        }
        buildCodeEditor(type) {
            let codeEditor = this.editor.CodeManager.getViewer('CodeMirror').clone();
            codeEditor.set({
                codeName: type === 'html' ? 'htmlmixed' : 'css',
                readOnly: false,
                theme: 'hopscotch',
                autoBeautify: true,
                autoCloseTags: true,
                autoCloseBrackets: true,
                styleActiveLine: true,
                smartIndent: true
            });
            return codeEditor;
        }
        buildtabs(type, editor, textArea) {
            const div_type = document.createElement('div');
            var class_type = "";
            if (type == 'html') {
                class_type = "tab-pane show active";
            } else {
                class_type = "tab-pane show active";
            }
            this.setAttributes(div_type, { "id": "nav-" + type, "class": class_type, "role": "tabpanel", "aria-labelledby": "nav-" + type + "-tab" });
            div_type.appendChild(textArea);
            return div_type;
        }
        refreshEditors() {
            this.htmlCodeEditor.editor.refresh();
            if (this.cssCodeEditor) {
                this.cssCodeEditor.editor.refresh();
            }


        }
        updateEditorContents(block) {
            if (!this.isShowing) return;
            if (!block) {
                this.htmlCodeEditor.setContent(this.getGrapesHtml());
            } else {
                this.htmlCodeEditor.setContent(this.getBlocksHtml());
            }
            if (this.cssCodeEditor) {
                this.cssCodeEditor.setContent(this.getCssElement());
            }
        }

        getCssElement() {
            let result = '';
            const config = this.editor.getConfig();
            const editor = this.editor;
            result = editor.CodeManager.getCode(editor.DomComponents.getComponent(),'css',{cssc:editor.CssComposer,
        wrapperIsBody:1,
        keepUnusedStyles:0});
            return result;

        }
        getGrapesHtml() {
            let result = '';
            const config = this.editor.getConfig();
            const editor = this.editor;
            const html = editor.getHtml();
            result += html;
            const js = "";
            result += js ? `<script>${js}</script>` : '';

            return result;
        }
        getBlocksHtml() {
            let result = '';
            const config = this.editor.getConfig();
            const editor = this.editor;
            const html = editor.CodeManager.getCode(editor.getSelected(), 'html');
            result += html;
            const js = "";
            result += js ? `<script>${js}</script>` : '';

            return result;
        }
        findWithinEditor(arg) {
            return this.editor.$(arg, this.editor.getEl());
        }
    }
    const editorCode = (editor, senderBtn, opts) => {
        if (senderBtn.id == 'code-editor') senderBtn.set('active', 0);
        let popup = new CodeViewer(editor, senderBtn, opts);
        let selected = editor.getSelected();
        let selectedstyle = editor.getSelectedToStyle();
        var _this = this;
        var block = 0;
        btnEdit.innerHTML = 'Update';
        btnEdit.className = pfx + 'btn btn-prim ' + pfx + 'btn-import';
        btnEdit.onclick = function() {
            const htmlCode = popup.htmlCodeEditor.editor.getValue();
            popup.previousHtmlCode = htmlCode;
            if (!block) {
                editor.setComponents(htmlCode.trim());
                const cssCode = popup.cssCodeEditor.editor.getValue();
                popup.previousCssCode = cssCode;
                editor.setStyle(cssCode);
            } else {
                selected.get('components').reset(htmlCode.trim());
            }
            let $content = editor.$(modal.getContentEl()).parents('.gjs-mdl-container');

            //display close button
            $content.find('.gjs-mdl-btn-close').get(0).style.display = 'block';
            if ($content.find('.gjs-mdl-btn-fulwith').length) $content.find('.gjs-mdl-btn-fulwith').remove();
            modal.close();
            block = 0;
            return false;

        };
        btnCancel.innerHTML = 'Cancel';
        btnCancel.className = pfx + 'btn btn-prim ' + pfx + 'btn-danger';
        btnCancel.onclick = function() {
            var newHtml = popup.htmlCodeEditor.editor.getValue();
            var htmlold;
            if(senderBtn.id == 'code-editor'){
                htmlold = editor.getHtml();
            }else{
                htmlold  = editor.CodeManager.getCode(editor.getSelected(), 'html');
            }
            if(newHtml.replace(/(\s+)/gm,'').replace(/(\r\n|\n|\r)/gm,'') !== htmlold.replace(/(\s+)/gm,'').replace(/(\r\n|\n|\r)/gm,'')){
                var conf = confirm('Old your change will be lost. Are you sure?');
                if (conf === true) {
                    modal.setContent('');
                    let $content = editor.$(modal.getContentEl()).parents('.gjs-mdl-container');
                    //display close button
                    $content.find('.gjs-mdl-btn-close').get(0).style.display = 'block';
                    if ($content.find('.gjs-mdl-btn-fulwith').length) $content.find('.gjs-mdl-btn-fulwith').remove();
                    modal.close();
                }
                block = 0;
                return false;
            }else{
                modal.setContent('');
                let $content = editor.$(modal.getContentEl()).parents('.gjs-mdl-container');
                //display close button
                $content.find('.gjs-mdl-btn-close').get(0).style.display = 'block';
                if ($content.find('.gjs-mdl-btn-fulwith').length) $content.find('.gjs-mdl-btn-fulwith').remove();
                modal.close();
            }
        };
        modal.setTitle('Edit code');
        let sections = document.createElement('sections');
        if (senderBtn.id == 'code-editor') {
            sections.innerHTML = `
              <nav>
                <div class="nav nav-tabs" id="nav-tab" role="tablist">
                  <a class="nav-item nav-link active" id="nav-html-tab" data-toggle="tab" href="#nav-html" role="tab" aria-controls="nav-html" aria-selected="true">Html</a>
                  <a class="nav-item nav-link" id="nav-css-tab" data-toggle="tab" href="#nav-css" role="tab" aria-controls="nav-css" aria-selected="false">Css</a>
                </div>
              </nav>`;
        } else {
            block = 1;
            sections.innerHTML = `
              <nav>
                <div class="nav nav-tabs" id="nav-tab" role="tablist">
                  <a class="nav-item nav-link active" id="nav-html-tab" data-toggle="tab" href="#nav-html" role="tab" aria-controls="nav-html" aria-selected="true">Html</a>
                </div>
              </nav>`;
        }

        if (!popup.htmlCodeEditor || !popup.cssCodeEditor) {
            const div = document.createElement('div');
            div.setAttribute('id', "nav-tabContent");
            div.setAttribute('class', "tab-content");
            popup.htmlCodeEditor = popup.buildCodeEditor('html');
            const htmlTextArea = document.createElement('textarea');
            div.appendChild(popup.buildtabs('html', popup.htmlCodeEditor, htmlTextArea));
            if (!block) {
                popup.cssCodeEditor = popup.buildCodeEditor('css');
                let cssTextArea = document.createElement('textarea');
                div.appendChild(popup.buildtabs('css', popup.cssCodeEditor, cssTextArea));
                popup.cssCodeEditor.init(cssTextArea);
            }

            popup.htmlCodeEditor.init(htmlTextArea);
            div.appendChild(btnEdit);
            div.appendChild(btnCancel);
            sections.append(div);
            modal.setContent(sections);
            popup.updateEditorContents(block);
        }
        modal.open();
        popup.refreshEditors();
        //fix click outsite popup
        let $content = editor.$(modal.getContentEl()).parents('.gjs-mdl-container');
        const outsisteClick = document.createElement('div');
        outsisteClick.setAttribute('class', 'gjs-mdl-backlayer');
        $content.append(outsisteClick);
        $content.find('.gjs-mdl-backlayer').on('click', function(e) { e.preventDefault();
            e.stopPropagation(); });
        editor.$(modal.getContentEl()).find('#nav-css').removeClass('active');
        //add event click on html btn
        editor.$(modal.getContentEl()).find('#nav-html-tab').on('click', function(e) {
            e.preventDefault();
            editor.$(modal.getContentEl()).find('#nav-css').removeClass('active');
            editor.$(modal.getContentEl()).find('#nav-css-tab').removeClass('active');
            editor.$(modal.getContentEl()).find('#nav-html-tab').addClass('active');
            editor.$(modal.getContentEl()).find('#nav-html').addClass('active');
        });
        //add event click on css btn
        editor.$(modal.getContentEl()).find('#nav-css-tab').on('click', function(e) {
            e.preventDefault();
            editor.$(modal.getContentEl()).find('#nav-html').removeClass('active');
            editor.$(modal.getContentEl()).find('#nav-html-tab').removeClass('active');
            editor.$(modal.getContentEl()).find('#nav-css-tab').addClass('active');
            editor.$(modal.getContentEl()).find('#nav-css').addClass('active');
        });
        //display close button
        $content.find('.gjs-mdl-btn-close').get(0).style.display = 'none';
        let _btnfulwith = document.createElement('div');
        _btnfulwith.setAttribute('class', 'btn gjs-mdl-btn-fulwith');
        _btnfulwith.innerHTML = `<i class="fal fa-expand"></i>`;
        if (!$content.find('.gjs-mdl-btn-fulwith').length) $content.find('.gjs-mdl-header').append(_btnfulwith);
        const icon = $content.find('.fal').get(0).className.replace('fa-compress', '').trim();
        $content.find('.fal').get(0).className = icon;
        const iconFulwith = $content.find('.gjs-mdl-dialog').get(0).className.replace('fulwith', '').trim();
        $content.find('.gjs-mdl-dialog').get(0).className = iconFulwith;
        _btnfulwith.onclick = function(e) {
            $content.find('.fal').get(0).classList.toggle('fa-compress');
            $content.find('.fal').get(0).classList.toggle('fa-expand');
            $content.find('.gjs-mdl-dialog').get(0).classList.toggle('fulwith');
            let findCodeMir = $content.find('.CodeMirror');
            if (findCodeMir.length > 1) {
                findCodeMir.each(finder => findCodeMir[finder].classList.toggle('CodeMirror-height'));
            } else {
                findCodeMir.toggleClass('CodeMirror-height');
            }
            e.preventDefault();
        };
    };
    cmdm.add('code-editor', {
        run: function(editor, senderBtn, opts) {
            editorCode(editor, senderBtn, opts)
        }
    });
    cmdm.add('block-editor', {
        run: function(editor, senderBtn, opts) {
            editorCode(editor, senderBtn, opts)
        }
    });

    // pnm.addButton('options', [{
    //     id: 'code-editor',
    //     className: 'fal fa-code',
    //     command: 'code-editor',
    //     attributes: {
    //         title: '',
    //       'data-tooltip-pos': 'bottom',
    //       'data-tooltip': 'Code Editor'
    //     }
    // }]);

}
