const allitems = {};
export default {
	extend: 't4.element',
	extendFn: ['init'],
	isComponent: el => {
		if (!el.className) return false;
		return el.classList.contains('carousel')
	},

	model: {
		defaults: {
			itemname: 'Slide',
			name: 'Carousel',
			smgroup: 'accordion',
            traits: [
            	'id',
                {
                	label:'carousel Type',
                	name:'carouselType',
                	type: 'select',
                	options: [
                		{name: 'Slide', value: 'slide'},
                		{name: 'Fade',value: 'carousel-fade'}
                	],
                	changeProp: 1,
                },
                {
                    label: 'Auto Play',
                    name: 'autoPlay',
                    type: 'checkbox',
                    defaults: 0,
                    changeProp: 1,
                },
                {
                    label: 'Indicators',
                    name: 'indicators',
                    type: 'select',
                    defaults: 'show',
                    options:[
                        {name: 'Show',value:'show'},
                        {name: 'Hide', value:'hidden'},
                    ],
                    changeProp: 1,
                },
                {
                    label: 'Controls',
                    name: 'controls',
                    type: 'select',
                    defaults: 'show',
                    options:[
                        {name: 'Show',value:'show'},
                        {name: 'Hide', value:'hidden'},
                    ],
                    changeProp: 1,
                },
                {
                    label: 'Interval',
                    name: 'interval',
                    changeProp: 1,
                    type: 'number'
                },
            ],
            selectable: true,
            
		},
		init() {
        	if (this.opt.temporary) return;
        	// init list
			this.updateChild(this)

        	this.detectCarouselType();
        	this.detectAutoPlay();
        	this.detectIndicators();
        	this.detectControls();
        	this.detectInterval();
        	this.listenTo(this,'change:carouselType',this.updateAttrCarousel);
        	this.listenTo(this,'change:autoPlay',this.updateAutoPlay);
        	this.listenTo(this,'change:indicators',this.updateIndicators);
        	this.listenTo(this, 'change:controls', this.updateControls);
        	this.listenTo(this, 'change:interval', this.updateInterval);
        },

		getItem(id) {
			const items = allitems[this.getId()];
			return id === undefined ? items : items[id];
		},
		getList() {
			const items = {};
			const inner = this.find('.carousel-inner'),
				handler = this.find('.carousel-indicators');

			let active = 0;

			if (!handler.length || !inner.length) return items;
			const handlers = handler[0].components();
			inner[0].components().each((c, i) => {
				const item = {content: c, handler: handlers.at(i)}
				if (item.handler) {
					item.handler.addAttributes({'data-target': '#' + this.getId(), 'data-slide-to': i});

					item.handler.set({
						selectable: false,
						copyable: false,
						removable: false,
						draggable: false,
						toolbar: []
					})

					item.content.set({
						selectable: true,
						draggable: false,
						name: this.get('itemname'),
						smgroup: 'accordion'
					})

					// current active
					if (c.getClasses().includes('active')) active = i;
				}

				items[i] = item;
			})
			
			this.set('current-slide', active);
			this.set('total-slides', inner[0].components().length);

			// control href
			this.find('[data-slide]').forEach(c => c.addAttributes({'data-target': '#' + this.getId()}))

			return items;
		},

		getContentCollection() {
			const inner = this.find('.carousel-inner');
			return inner[0].components();
		},

		getContentItem(id) {
			return this.getItem(id).content;
		},

		initList() {
			if (this.listInited) return;

			this.setId(this.getId());

			allitems[this.getId()] = this.getList();

			this.listenTo(this.getContentCollection(), 'add', this.cloneItem)
			this.listenTo(this.getContentCollection(), 'remove', this.removeItem)
			this.listenTo(this.getContentCollection(), 'list:moved', this.movedItem)

			this.getContentCollection().parent.set('sortable', 1);
	
			this.listInited = true;
		},

		setActive(id, noSelectItem) {
			if (id == this.get('current-slide')) return;
			// remove current active

			const citem = this.getItem(this.get('current-slide'));
			if (citem.handler) citem.handler.view.$el.removeClass('active');
			citem.content.view.$el.removeClass('active');
			// add new active
			const item = this.getItem(id);
			if (item.handler) item.handler.view.$el.addClass('active');
			item.content.view.$el.addClass('active');

			this.set('current-slide', id);

			// set active
			if (!noSelectItem) {
				const item = this.getContentItem(id);
				if (item.view) setTimeout(() => {item.view.$el.trigger('click')}, 50)
			}
		},


		removeItem(model, coll, opt) {
			if (opt.temporary) return;

			const at = opt.index,
				item = this.getItem(opt.at);
			item.handler && item.handler.remove();

			// remove from cached list
			const items = this.getItem();

			// update new index
			for (var i=0; i < this.get('total-slides')-1; i++) {
				items[i] = items[i+1];
				items[i].handler.addAttributes({'data-slide-to': i});
			}

			// delelte
			delete items[this.get('total-slides')-1];
			// decrease total
			this.set('total-slides', this.get('total-slides') - 1);

			// get last item
			if (this.get('total-slides')) {
				this.setActive(0);
			} else {
				this.remove();
			}			
		},

		cloneItem(model, coll, opt) {
			if (opt.temporary) return;

			// clone handler and put item into list
			const item = this.getItem(opt.at - 1),
				clonedHandler = item.handler.clone(),
				newItem = {};
				clonedHandler && clonedHandler.removeClass('active');
				model.removeClass('active');
			item.handler.collection.add(clonedHandler, {at: opt.at});
			newItem.handler = clonedHandler;
			newItem.content = model;
			newItem.handler.addAttributes({'data-target': '#' + this.getId(), 'data-slide-to': opt.at});

			// process as an array
			const items = this.getItem();

			// update new index
			for (var i=0; i < this.get('total-slides')-1; i++) {
				items[i].handler.addAttributes({'data-slide-to': i});
			}
			// add new one
			items[this.get('total-slides')] = newItem;
			// increase total
			this.set('total-slides', this.get('total-slides') + 1);

			this.setActive(opt.at);
		},

		movedItem(model, coll, opt) {
			if (opt.temporary) return;

			const items = this.getItem(),
				at = opt.at,
				to = opt.to,
				item = items[at],
				handler = item.handler;

			// move content tab
			const 
				handler_coll = handler.collection,
				silent = {silent: true};
			// remove current pos
			handler_coll.remove(handler);
			// add to new post
			handler_coll.add(handler, {at: to, ...silent});

			// update order for item
			items[at] = items[to];
			items[at].handler.addAttributes({'data-slide-to': at});
			items[to] = item;
			items[to].handler.addAttributes({'data-slide-to': to});

			// update view
			this.view.render();
			// active
			this.setActive(to);
		},
		detectCarouselType(){
			let attrClass = this.getAttr('class').split(' ');
			if(attrClass.indexOf('slide') != -1){
				this.set('carouselType','slide');
			}else if(attrClass.indexOf('carousel-fade') != -1){
				this.set('carouselType','carousel-fade');
			}
		},
		updateAttrCarousel() {
			const val = this.get('carouselType');
			if(val == 'carousel-fade'){
				this.removeClass('slide');

			}else if(val == 'slide'){
				this.removeClass('carousel-fade');
			}
			this.addClass(val);
		},
		detectAutoPlay(){
			let autoPlay = (this.getClasses().indexOf('btn-pause') != -1) ? false : true;
			if(autoPlay){
				this.set('autoPlay',true);
			}else{
				this.set('autoPlay',false);
			}
		},
		updateAutoPlay(){
			let value = this.get('autoPlay');
			if(!value){
				this.addClass('btn-pause');
				this.setAttr('data-ride',false);
			}else{
				this.removeClass('btn-pause');
				this.setAttr('data-ride','carousel');
			}
            return this;
		},
		detectControls(){
			let controlsModel = this.components().find(comp => comp.getClasses().indexOf('carousel-controls') != '-1');
        	if(controlsModel && controlsModel.getClasses().indexOf('hidden') != '-1'){
            	this.set('controls','hidden');
            }else{
                this.set('controls','show');
            }
		},
        updateControls() {
            let value = this.get('controls');
            let controlsModel = this.components().filter(comp => comp.getClasses().indexOf('carousel-controls') != '-1');

            if(!controlsModel.length) return this;
            if(value && value == 'hidden'){
                controlsModel.forEach(mod => mod.addClass('hidden'));
            }else{
            	controlsModel.forEach(mod => mod.removeClass('hidden'));
            }
            return this;
        },
        detectIndicators(){
        	let indicatorModel = this.components().find(comp => comp.getClasses().indexOf('carousel-indicators') != '-1');
        	if(indicatorModel && indicatorModel.getClasses().indexOf('hidden') != '-1'){
            	this.set('indicators','hidden');
            }else{
                this.set('indicators','show');
            }
        },
		updateIndicators() {
            let value = this.get('indicators');
            let model = this.components().find(comp => comp.getClasses().indexOf('carousel-indicators') != '-1');
            if(!model) return this;
            if(value && value != null){
                if(value == 'hidden'){
                    model.addClass(value);
                }else{
                    model.removeClass('hidden');
                }
            }
            return this;
		},
		detectInterval(){
			let interval = this.getAttr('data-interval');
			if(!interval){
				this.set('interval',4000);
			}else{
				this.set('interval',interval);
			}
		},
		updateInterval(){
			let interval = this.get('interval');
			if(interval){
				this.setAttr('data-interval',interval);
			}else{
				this.setAttr('data-interval',4000);
			}
		},

	},

	view: {
		events: {
			'click [data-slide="next"]': 'toggleNext',
			'click [data-slide="prev"]': 'togglePrev',
			'click [data-slide-to]': 'toggle',	
		},

		toggleNext() {
			let active = this.model.get('current-slide') + 1;
			if (active >= this.model.get('total-slides')) active = 0;
			this.model.setActive(active);
		},

		togglePrev() {
			let active = this.model.get('current-slide') - 1;
			if (active < 0) active = this.model.get('total-slides') - 1;
			this.model.setActive(active);
		},

		toggle(e) {
			const slideto = e.target.getAttribute('data-slide-to');
			let active = parseInt(slideto);
			if (active >= this.model.get('total-slides')) active = 0;
			if (active < 0) active = this.model.get('total-slides') - 1;

			this.model.setActive(active);
		},
		onRender() {
            if (this.model.opt.temporary) return;
            this.model.initList();
        }

	}
}