export default {
	model: {

		getAttr(name) {
            return this.getAttributes()[name];
        },

        setAttr(name, value) {
            let attr = {};
            attr[name] = value;
            this.addAttributes(attr);
        },

		hasAttr(name) {
        	return this.getAttributes()[name] !== undefined 
    	}
	},

	view: {

	}
}