// import module from './module';
// import modules from './modules';
// import component from './component';
// import message from './message';
// import readmore from './readmore';
import editorxtd from './editorxtd';

import jdoc from './model/jdoc';
//import shortcode from './model/shortcode';
//import shareblock from './model/shareblock';
//import component from './model/component';
import message from './model/message';
import module from './model/module';
import position from './model/modules';

//import shareblockView from './view/shareblock';

export default (editor, config = {}) => {
  const dc = editor.DomComponents;
  const defaultView = dc.getType('default').view;

  const jcoms = [
    {
      id: 'jdoc',
      model: jdoc(editor),
      view: defaultView,
    },
    // {
    //   id: 'shortcode',
    //   model: shortcode(editor),
    //   view: defaultView,
    // },
    // {
    //   id: 'shareblock',
    //   model: shareblock(editor),
    //   view: shareblockView(editor),
    // },
    {
      id: 'message',
      model: message(editor),
      view: defaultView,
    },
    // {
    //   id: 'component',
    //   model: component(editor),
    //   view: defaultView,
    // },
    {
      id: 'module',
      model: module(editor),
      view: defaultView,
    },
    {
      id: 'position',
      model: position(editor),
      view: defaultView,
    },
  ];

  jcoms.forEach(com => {
    dc.addType(com.id,{
      model: com.model,
      view: com.view,
    });
  });


  // module(editor, config);
  // modules(editor, config);
  // component(editor, config);
  // message(editor, config);
  // readmore(editor, config);
  editorxtd(editor, config);


  // add blocks
  const bm = editor.BlockManager;

  bm.add('joomla-module', {
    label: 'Module',
    group: 'Element',
    category: 'Joomla',
    attributes: {class:'fal fa-dice-d6'},
    content: { type: 'module' }
  });  

  bm.add('joomla-position', {
    label: 'Position',
    group: 'Element',
    category: 'Joomla',
    attributes: {class:'fal fa-cubes'},
    content: { type: 'position' }
  });  
/*
  bm.add('joomla-component', {
    label: 'Component',
    group: 'Element',
    category: 'Joomla',
    attributes: {class:'fal fa-stream'},
    content: { type: 'component' }
  });  
*/
  bm.add('joomla-message', {
    label: 'Message',
    group: 'Element',
    category: 'Joomla',
    attributes: {class:'fal fa-comment-exclamation'},
    content: { 
      type: 'message',
    }
  });  


  config.xtdbuttons && config.xtdbuttons.forEach(button => {
    let attributes;
    switch (button.name){
      case 'module':
        attributes = { class: 'fal fa-dice-d6' };
        break;
      case 'menu':
        attributes = { class: 'fal fa-bars' };
        break;
      case 'contact':
        attributes = { class: 'fal fa-address-card' };
        break;
      case 'article':
        attributes = { class: 'fal fa-newspaper' };
        break;
      case 'image':
        attributes = { class: 'fal fa-image' };
        break;
      case 'page-break':
        attributes = { class: 'fal fa-pager' };
        break;
      case 'read-more':
        attributes = { class: 'fal fa-info-circle' };
        break;
      default:
        attributes = { class: 'fal fa-empty-set' };
        break;
    }
    bm.add('joomla-xtd-' + button.name, {
      label: button.label,
      group: 'Element',
      attributes: attributes,
      category: 'XTD Buttons',
      content: { 
        type: 'editorxtd',
        name: button.name
      }
    });  

  })

}
