
import { keyIcons, commandNameIcons, typeIcons, tabIcon } from './config';

export default (editor, opts = {}) => {
  var loadIcon = editor.Config.loadIcons;
  if(!loadIcon.material_icons.material_icons && !loadIcon.awesome_icons.awesome_icons) return;
  const dc = editor.DomComponents;
  const defaultType = dc.getType('text');
  const defaultModel = defaultType.model;
  const { toolbarBtnIcons } = opts;

  dc.addType(typeIcons, {
    model: defaultModel.extend({
      defaults: {
        tagName: 'i',
        ...defaultModel.prototype.defaults,
        name: 'Icon',
        smgroup: 'icon',
        editable: true,
        textable: 1,
        selectable:true,
        ...opts.propsIcons
      },

      /**
       * Initilize the component
       */
      init() {
        if (this.opt.temporary) return;
        this.listenTo(this, `change:${keyIcons}`, this.onIconsChange);
        let icons = this.getClasses()[0] || "";
        if(icons == 'material-icons'){
          this.set('tabicon','material');
          this.set('iconVal',this.get('content'));
        }else if('fa'){
          this.set('tabicon','fa');
          this.set('iconVal',this.getClasses()[0].replace('gjs-comp-selected', ''));
        }
      },

      /**
       * Callback to launch on keyIcons change
       */
      onIconsChange() {
        let icons = this.get(keyIcons);
        this.set('iconVal',icons);
        let iconType = '';
        let classes = this.getClasses();
        if(icons.match(/(^|\s)fa(\s|\-|$)/)){
          iconType = 'fa';
        }
        classes.splice(0,2);
        this.removeClass(this.getClasses());
        switch (iconType) {
          case 'fa':
            this.addClass(this.get(keyIcons));
            if(classes.length){
              this.addClass(classes.join(" "));
            }
            this.set('content','');
            break;
          default:
            this.addClass('material-icons');
            this.set('content',this.get(keyIcons));
            break;
        }
        return this;
      },
      toHTML() {
        this.trigger('sync:content', { silent: 1 });
        return defaultModel.prototype.toHTML.apply(this, arguments);
      }
    }, {
      /**
       * The component can be used only if requested explicitly via `type` property
       */
      isComponent(el) {
        if (!el.tagName) return false;
        return (el.className && (el.className == 'material-icons' || (el.className.match(/(^|\s)(fa|fal|fab|far|fas)(\s|\-|$)/) ? true : false)));

      }
    }),

    view: defaultType.view.extend({
      events: {
        dblclick: 'onActive',
      },
      onActive() {
        //this.model.parent().set('editable', false);
        const target = this.model;
        this.em.get('Commands').run(commandNameIcons, { target });
      },
    })
  });
}
