export default (editor, config = {}) => {
	const sm = editor.StyleManager;
	const clearProp = 'data-clear-style';
	const { propertys } = config;

	const selectType = sm.getType('select');
	const selectModel = selectType.model;
	const selectView = selectType.view;
	sm.addType('fontfamily', {
		model: selectModel.extend({
			defaults: {
				...selectModel.prototype.defaults,
				name: 'Font',
				property: "font-family",
				type: 'select',
				id: "font-family",
				value : 'Arial, Helvetica, sans-serif',
				defaults : 'Arial, Helvetica, sans-serif',
				full: 1,
				info: 'Font Family',
			},
			init(...args){

				selectModel.prototype.init.apply(this, args);
				let list = this.getOptions();
				let fonts = Object.values(editor.SettingManager.fontdefaults);
				fonts.forEach(font => {
					list.push({value: font.name, name: font.name});
				});
				this.defaultOptions = list;
			}

		}),
		view: selectView.extend({
			init(...args) {
			    this.listenTo(this.model, 'change:value', this.updateWeights);
			},

			updateWeights(){
				let listweightProperty = [
		            { value: '100', name: 'Thin' },
		            { value: '200', name: 'Extra-Light' },
		            { value: '300', name: 'Light' },
		            { value: '400', name: 'Normal' },
		            { value: '500', name: 'Medium' },
		            { value: '600', name: 'Semi-Bold' },
		            { value: '700', name: 'Bold' },
		            { value: '800', name: 'Extra-Bold' },
		            { value: '900', name: 'Ultra-Bold' }
		          ];
				let value = this.model.getFullValue();
				let googlfonts = JSON.parse(this.em.get('Editor').Config.settingsManager.loadgooglefont);
				let fontArr = Object.values(googlfonts);
				
				
				let weightProperty = this.getProps('font-weight');
				if(!weightProperty) return;
				if(value === weightProperty.fontvalue) return;

				weightProperty.fontvalue = value;
				let list = weightProperty.getOptions();
				if(fontArr.filter(font => font.name == value).length){
		      		let fontWeight = fontArr.find(font => font.name == value).weight;
		      		let listWeight = [];
		      		fontWeight.forEach( function(el, index) {
		      			let weightVal = list.find(w => w.value == el);
		      			weightVal && listWeight.push(weightVal);
		      		});
	    				if(!listWeight.length) listWeight = list;
		      		this.em.get('Editor').SettingManager.loadFontWeight(this.em.get('Editor').StyleManager,listWeight);
		      	}else{
		      		this.em.get('Editor').SettingManager.loadFontWeight(this.em.get('Editor').StyleManager,listweightProperty);
		      	}
			},
			getProps(propertyName){
				let sectors = this.em.get('Editor').StyleManager.getSectors().models;
				let weightProperty = '';
				sectors.forEach(sec => {
					let name = sec.get('id');
					let props = this.em.get('Editor').StyleManager.getProperty(name, propertyName);
					if(typeof props.id !== 'undefined') weightProperty = props;
				});
				return weightProperty;
			}
		}),
	});
}