export default {
	extend: 'text',
	model: {
		defaults: {
            smgroup: 'text',
            unstylable: [
                "background",
                "background-image",
                "opacity",
            ],
			traits: [
                {
                    label: "Alert Type",
                    name: "alert-type",
                    type: "select",
                    isClass: 1,
                    options: [{
                        name: "Default",
                        value: ""
                    }, {
                        name: "Primary",
                        value: "alert-primary"
                    }, {
                        name: "Secondary",
                        value: "alert-secondary"
                    }, {
                        name: "Dark",
                        value: "alert-dark"
                    }, {
                        name: "Light",
                        value: "alert-light"
                    }, {
                        name: "Info",
                        value: "alert-info"
                    }, {
                        name: "Success",
                        value: "alert-success"
                    }, {
                        name: "Warning",
                        value: "alert-warning"
                    }, {
                        name: "Danger",
                        value: "alert-danger"
                    }],
                }
            ],
            attributes: {
            	role: 'alert'
            }
		},
        init() {
            if (this.opt.temporary) return;

            // bind traits for class
            const traits = this.get('traits');
            const classes = [];
            traits.each(trait => {
                if (trait.get('isClass')) {
                    // make this trait as hideProp
                    trait.set('changeProp', 1);
                    // add to classes option
                    // classes.push(tr);
                    this.detectClass(trait);
                }
            })

            this.updateTraits();

        },
        detectClass(trait) {
            const classes = trait.get('options').map(opt => typeof opt == 'string' ? opt : opt.value);
            const name = trait.get('name');

            this.getClasses().forEach(cls => {
                if (classes.indexOf(cls) >= 0) {
                    this.set(name, cls);
                }
            })

            this.on('change:' + name, () => {
                const clss = this.getClasses(),
                    prevCls = this.previous(name),
                    cls = this.get(name),
                    idx = clss.indexOf(prevCls);

                if (idx > -1) clss[idx] = cls;
                else clss.push(cls);
                this.setClass(clss);
            })
        },

        updateTraits() {
            const event = 'change:device';
            const toListen = [this.em, event, this.updateTraits];
            this.stopListening(...toListen);

            if (this.traitsOnDevice == undefined) {
                this.traitsOnDevice = false;
                const traits = [...this.get('traits').toArray()];
                traits.forEach(trait => {
                    if (trait.get('device')) this.traitsOnDevice = true;
                })
                if (this.traitsOnDevice) this.traits = traits;
            }

            if (this.traitsOnDevice) {
                let traits = [],
                    device = editor.getDevice() || 'Desktop';

                this.traits.forEach(trait => {
                    if (trait.get('device') && trait.get('device').indexOf(device) == -1) return;
                    traits.push(trait);
                })

                this.set('traits', traits);
                this.em.trigger('component:toggled');

                this.listenTo(...toListen);
            }
        },

        getAttr(name) {
            return this.getAttributes()[name];
        },

        setAttr(name, value) {
            let attr = {};
            attr[name] = value;
            this.addAttributes(attr);
        }
	},
	isComponent: el => {
        return (el.matches && el.matches('[role="alert"]'));
    }
}