import Overlays from './overlay';
export default (editor, config = {}) => {
	
	const propsOpacity = {
			name: 'Opacity',
	        type: 'slider',
	        oltype: 'opacity',
	        property: 'opacity',
	        defaults: 1,
	        step: 0.01,
	        max: 1,
	        min:0,
	      };
	let options = {
		propertys:{
			'video': [{
				name: 'video Source',
				oltype: 'video',
				type: 'select',
				full: 1,
				options: [
					{value: 'youtube'},
					{value: 'vimeo'},
				],
				defaults: 'youtube',
			}, {
				name: 'videoID',
				oltype: 'id',
				full: 1,
				type: 'base',
				defaults: '12344'
			},propsOpacity],
			'img': [{name: 'Background image',oltype: 'url',type: 'file',},
			{
				name: 'Background size',
				oltype: 'size',
				type: 'select',
				options: [
					{value: 'auto'},
					{value: 'cover'},
					{value: 'contain'},
				],
				defaults: 'auto'
			}, {
				name: 'Background position',
				oltype: 'position',
				type: 'select',
				defaults: 'center top',
				options: [
					{value: 'left top'},
					{value: 'left center'},
					{value: 'left bottom'},
					{value: 'right top'},
					{value: 'right center'},
					{value: 'right bottom'},
					{value: 'center top'},
					{value: 'center center'},
					{value: 'center bottom'},
				]
			}, {
				name: 'Background repeat',
				oltype: 'repeat',
				type: 'select',
				defaults: 'no-repeat',
				options: [
					{value: 'repeat'},
					{value: 'repeat-x'},
					{value: 'repeat-y'},
					{value: 'no-repeat'},
				]
			}, {
				name: 'Background attachment',
				oltype: 'attachment',
				type: 'select',
				defaults:'scroll',
				options: [
					{value: 'scroll'},
					{value: 'fixed'},
					{value: 'local'},
				]
			},propsOpacity],
			'file': [{name: 'Cover',oltype: 'cover',type: 'base',defaults: 'test'},
			 		{name: 'Webm',oltype: 'webm',type: 'base',defaults: 'test'},
			 		{name: 'mp4',oltype: 'mp4',type: 'base',defaults: 'test'},
			 		{name: 'OGG',oltype: 'ogg',type: 'base',defaults: 'test'}]
		}
	};
	const opts = {...options, ...config};	
	Overlays(editor,opts);
}