import jdocModel from './jdoc';
export default (editor) => {
    const jdoc = jdocModel(editor);

    return jdoc.extend({
        defaults: {
            ...jdoc.prototype.defaults,
            name: 'Message',
            type: 'message',
            attributes: {
                type: 'message'
            }
        },

    }, {
        isComponent(el) {
            return el.tagName == 'JDOC:INCLUDE' && el.getAttribute && el.getAttribute('type') && el.getAttribute('type').toLowerCase() == 'message';
        },
    })
}