import Backbone from 'backbone';

export default (editor, options = {}) => {
    let bm = editor.BlockManager;
    var panelManager = editor.Panels;
    var cmdm = editor.Commands;


    //set data tooltip

    let btnViewArr = ['open-layers', 'open-sm', 'sw-visibility', 'fullscreen','preview'];
    btnViewArr.forEach(btn => {
        let options, pos;
        if (btn == 'sw-visibility' || btn == 'fullscreen' || btn == 'preview') {
            options = 'options';
            pos = 'bottom';
        } else {
            options = 'views';
            pos = 'right';
        }

        let swv = editor.Panels.getButton(options, btn);
        if (btn == 'open-layers') {
            swv.get('attributes').class = 'fal fa-layer-group';
            swv.get('attributes').title = 'Layer Manager';
        }
        if (btn == 'open-sm') {
            swv.get('attributes').title = 'Style Manager';
        }
        if (swv) {
            let swvTitle = swv.get('attributes').title;
            swv.get('attributes')['data-tooltip-pos'] = pos;
            swv.get('attributes')['data-tooltip'] = swvTitle;
            swv.get('attributes').title = '';
        }

    });
    // make block categories view as accordion
    const categories = bm.getCategories();
    categories.each(category => {
        category.set('open', false).on('change:open', opened => {
            opened.get('open') && categories.each(category => {
                category !== opened && category.set('open', false)
            })
        });
        if(category.id == 'Basic') category.set('open',true);
    })
    const blockFree = ["core_block_call-to-action_style1", "core_block_clients_style1", "core_block_contact-info_flat5", "core_block_feature-intro_flat13"];
    var T4config = JSON.parse(localStorage.getItem("t4bConfig")) || {};

    const on_demo = () => location.hostname.match(/.*\.demo\.t4\-builder\.joomlart\.com/);
    const is_loggedin = () => !!T4config.user_type;
    const allow_use_pro = () => (T4config.user_type && T4config.user_type != "Free") || on_demo();


    // generate group category
    const apply_blocks_permission = () => {
        bm.getAll().each(block => {
            var group = block.get('group');
            const attrs = {...block.get('attributes')};
            if (!group || group == 'Layout') {
                group = 'Element';
                block.set('group', group);
            }
            if( !block.get("isfree") && block.get('group') != "Element" ){                
                block.set('ispro', 1);
                let cls = attrs.class || '';
                // remove all pros class
                cls = cls.replace(/t4b-block__pro[^\s]*/g, '').trim();
                cls += " t4b-block__pro";

                if (allow_use_pro()) {
                    block.set("disable",false);
                } else {
                    if (!is_loggedin()) {
                        cls += ' t4b-block__pro__nopermission t4b-block__pro__needlogin';
                        block.set("disable",true);
                    } else {
                        cls += ' t4b-block__pro__nopermission t4b-block__pro__needupgrade';
                        block.set("disable",true);
                    }
                }

                attrs.class = cls.trim();
                block.set('attributes', attrs);
                // if(blocks.get('attributes').class &&  !blocks.get('attributes').class.match(/t4b-block__lock/g)){
                //     blocks.get('attributes').class += " t4b-block__lock gjs-block--lock"; 
                // }else{
                //     blocks.get('attributes').class = "t4b-block__lock gjs-block--lock"; 
                // }
                // if((!T4config.user_type || T4config.user_type == "Free")){
                //     blocks.set("disable",true);
                // }
            }
        });
    }
    apply_blocks_permission();

    const startBindingModalEvent = () => {
        editor.$('body').addClass('t4b-block__pro_popup')

        editor.$('#t4b-login-form__btn-login').on('click', e => {
            const login = encodeURIComponent(jQuery("#t4b-login-form__login").val()), 
                pass = encodeURIComponent(jQuery('#t4b-login-form__pass').val()),
                //url = `${t4bConfig.remote_url}member/builderbundle/get-access-token`;
                url = `${T4config.remote_url}member/builderbundle/get-access-token?login=${login}&pass=${pass}`,
                $form = editor.$('.t4b-login-form');
            
            $form.addClass('loading');

            fetch(url, {
                method: 'GET',
                //body: JSON.stringify({login, pass})
            }).then(res => res.json())
            .then(data => {
                $form.removeClass('loading');
                console.log('login: ', data)
                if (data.status == '200') {
                    data.data.login_status = true;
                    data.data.remote_url = T4config.remote_url;
                    if(data.data.user_type.toLowerCase() != 'free'){
                        data.data.user_type_pk = "pro";
                    }else{
                        data.data.user_type_pk = "free";
                    }
                    T4config = data.data;
                    localStorage.setItem('t4bConfig', JSON.stringify(T4config));

                    // success
                    apply_blocks_permission();
                    editor.Modal.close();
                } else {
                    // show error message
                    editor.$('#t4b-login-form__message').html(data.message).removeClass('hide')
                    return false;
                }
            })
            .catch(err => {
                $form.removeClass('loading');
                editor.$('#t4b-login-form__message').html(err.message).removeClass('hide')
            });  
        });
    }

    const stopBindingModalEvent = () => {
        editor.$('#t4b-login-form__btn-login').off('click');
        editor.$('body').removeClass('t4b-block__pro_popup');
    }

    editor.$('body').on('mousedown', '.t4b-block__pro__nopermission', e => {
        const $el = editor.$(e.target).closest('.t4b-block__pro__nopermission');
        let content = '';
        if ($el.hasClass('t4b-block__pro__needlogin')) {
            content = `
            <div class="t4b-login-form">
              <div class="alert alert-info">
                <p>If you are free users, upgrade to <strong>Pro</strong> to access all <strong>Pro Website Bundles &amp; Blocks</strong>. <a href="https://www.joomlart.com/t4-page-builder" title="T4 Page Builder Pricing" target="_blank">View Pricing</a></p>
              </div>

                <div class="t4b-login-form__username">
                    <span class="add-on"><i class="fal fa-lock"></i></span>
                    <input type="text" id="t4b-login-form__login" name="am_admin_login" size="18" value="" autofocus="" placeholder="Email or Username">
                </div>
                <div class="t4b-login-form__password">
                    <span class="add-on"><i class="fal fa-key"></i></span>
                    <input type="password" id="t4b-login-form__pass" name="am_admin_passwd" size="18" placeholder="Password">
                </div>
                <div id="t4b-login-form__message" class="alert alert-error hide"></div>
                <div class="t4b-login-form__actions">
                    <input id="t4b-login-form__btn-login" class="btn btn-primary" type="button" name="submit" value="Log In">
                </div>
                <div class="t4b-login-form__links">
                  <a class="btn-link" href="https://www.joomlart.com/member/signup/free" target="_blank" title="Free Signup">Don't have account</a>
                  <a class="btn-link" href="https://www.joomlart.com/member/login?sendpass" target="_blank" title="Lost your password">Forgot password?</a>
                </div>
            </div>
            `;
        } else if ($el.hasClass('t4b-block__pro__needupgrade')) {
            content = `
            <div class="t4b-upgrade-message">
                Upgrade to use pro blocks. <a href="https://www.joomlart.com/t4-page-builder" target="_blank">Upgrade now</a>
            </div>
            `;
        }

        if (content) {
            const modal = editor.Modal;
            modal.setTitle('JoomlArt Memeber Login');
            modal.setContent(content);
            modal.open().onceClose(stopBindingModalEvent);
            startBindingModalEvent();
        }

    })

    const getTitle = (editor, cmdBlock) => {
        let commandBlock = editor.Panels.getButton('views', 'open-' + cmdBlock);
        let title = commandBlock.get('attributes')['data-tooltip'];
        return title;
    }

    //  overwrite BlockManager render function
    bm._render = bm.render;
    bm.render = (blocks, opts = {}) => {
        bm._render(blocks, opts);

        // Add category attribute
        var bmCon = bm.getContainer(),
            cats = bmCon.querySelectorAll('.gjs-block-category');
        cats.forEach(cat => {
            var cattitle = cat.querySelector('.gjs-title').innerText.toLowerCase().replace(/\s/, '-');
            cat.setAttribute('data-cat', cattitle);
            var cate_title = cat.querySelector('.gjs-title').innerText.replace(/-/g, ' ');
            cat.querySelector('.gjs-title').innerText = cate_title;
        })

    }

    bm.selectFilter = (editor) => {
        let bm = editor.BlockManager;
        let group = bm.activeGroup;
        if (!group) {
            group = 'Element'
        }
        let cate = [];
        let blocks = bm.getAll().filter(bl => {

            if (bl.get('group') == group) {
                var cat = bl.get('category').id;
                if (cate.indexOf(cat) == -1) cate.push(cat);
            }
        });
        let options = '<option value="1">All (' + bm.filterCount(group) + ')</option>';
        const filterSector = document.createElement('div');
        filterSector.setAttribute('class', 'gjs-field gjs-select gjs-filter-blocks');
        categories.each(category => {
            if (cate.indexOf(category.id) !== -1) {
                options += '<option value="' + category.id + '">' + category.id + ' (' + bm.filterCount(group, category.id) + ')' + '</option>';
            }

        })
        //const container = editor.$('.gjs-pn-views-container')[0];
        const container = editor.Commands.get('open-blocks').blocks;
        if (container.querySelector('.gjs-filter-blocks')) { container.querySelector('.gjs-filter-blocks').remove(); }
        const selectFilter = document.createElement('select');
        selectFilter.innerHTML = options;

        selectFilter.onchange = function() {
            let group = bm.activeGroup;
            let blockFilter, blockFilters;

            if (this.value == '1') {
                blockFilter = bm.getAll().filter(block => block.get('group') === group);
            } else {
                blockFilter = bm.getAll().filter(block => block.get('group') === group && block.get('category').id === this.value);
                categories.find(cat => cat.id === this.value).set('open', true);
            }
            if (group == 'Element') {
                let basicCate = blockFilter.filter(b => b.get('category').id == 'Basic');
                let typoCate = blockFilter.filter(b => b.get('category').id == 'Typography');
                let Elgroups = [...basicCate, ...typoCate];
                blockFilters = Elgroups.concat(blockFilter);
            } else {
                blockFilters = blockFilter;
            }
            bm.render(blockFilters);

        };

        filterSector.appendChild(selectFilter);
        var selArrow = document.createElement('div');
        selArrow.innerHTML = '<div class="gjs-d-s-arrow">';
        selArrow.className = 'gjs-sel-arrow';
        filterSector.appendChild(selArrow);

        container.prepend(filterSector);
    }
    bm.filterCount = (gr, categoryId = '') => {
        let count;
        let blocks = bm.getAll().filter(bl => bl.get('group') == gr);
        let blockCount = bm.getAll().filter(bl => bl.get('group') == gr && bl.get('category').id == categoryId);

        if (!categoryId) {
            count = blocks.length;
        } else {
            count = blockCount.length;
        }
        return count;
    }

    bm.renderGroup = (group, cate = '') => {
        bm.activeGroup = group;
        var ElRender, ElRenders;
        if (cate) {
            ElRender = bm.getAll().models.filter(block => block.get('group') == bm.activeGroup && block.get('category').id == cate);
        } else {
            ElRender = bm.getAll().models.filter(block => block.get('group') == bm.activeGroup);
        }

        if (group == 'Element') {
            let basicCate = ElRender.filter(b => b.get('category').id == 'Basic');
            let typoCate = ElRender.filter(b => b.get('category').id == 'Grid');
            let typoExtra = ElRender.filter(b => b.get('category').id == 'Extra');
            let Elgroups = [...basicCate, ...typoCate, ...typoExtra];
            ElRenders = Elgroups.concat(ElRender);
        } else {
            ElRenders = ElRender;
        }
        
        bm.render(ElRenders);
    }

    cmdm.add('open-group-blocks', {
        run: function(editor, sendbtn, options) {
            const group = sendbtn.get('group') || 'Element';
            const bm = editor.BlockManager;
            editor.runCommand('open-blocks');
            const blocksEl = bm.getContainer();
            blocksEl.className = 'gjs-group-' + group;
            blocksEl.setAttribute('group', group);
            bm.activeGroup = group;
            bm.selectFilter(editor);
            bm.renderGroup(group);
        },
        stop: function() {
            editor.stopCommand('open-blocks');
        }
    });

    editor.on('load', e => {
        // add Block header
        var $container = editor.Panels.getPanel('views-container').view.$el;
        var title = document.createElement('div');
        title.className = "gjs-block-header";
        $container.prepend(title);

        editor.Panels.getPanel('views').get('buttons').forEach(btn => {
            btn.on('change:active', () => {
                if (btn.get('active')) {
                    const help = (btn.get('helpkey')) ? '<i class="t4-helper" key="' + btn.get('helpkey') + '">?</i>' : '';
                    title.innerHTML = '<h2 class="block-title">' + btn.get('label') + '</h2>' + help;
                }
            })
        })
    });

}
