//import Base from './base';
import updateTraits from './helper/traits';
import {getAttr, setAttr, hasAttr} from './helper/attr';

export default {
	model: {
		defaults: {
			smgroup:'text',
		},

		init() {
			this.em.getEditor().DomComponents.getType('default').model.prototype.init.apply(this, arguments);

			if (this.opt.temporary) return;

			const bid = this.get('bid') || getAttr(this, 't4-bid'),
				block = bid ? this.em.get('t4-config').blocks.find(bl => bl.id == bid) : null,
				type = (block ? (block.group == 'Block' ? block.category + ': ' : '') + block.label : '');
			
			if (type) {
				this.set('name', type)
				this.set('t4-type', block.group)
				// make droptable if this is the blank box
				if (!this.get('editable') && this.components().length == 0) {
					this.set('droppable', true);
				}
			}
			this.updateChild(this, block, "default")
		},

		updateChild(model, block, type, parent) {
			if (hasAttr(model, 't4-container')) {
				model.set('droppable', true);
				return;
			}
			if(this)
			
			var _p = parent;
			if (type && block) {
				_p = model;
				model.set('selectable', true);
				const stype = type.match(/:/) ? type.split(':')[1] : type;
				if (stype != 'default') model.set('name', stype);
				updateTraits(model, {draggable: type == 'default' ? null : '[t4-list="' + type + '"]', model: block.model && block.model[stype]});
				setAttr(model, 't4-type', type);
			}

			const mapfields = getAttr(model, 't4-map'),
				map = {};

			if (parent && mapfields) {
				if (mapfields) {
                    mapfields.split(';').forEach(s => {
                        var arr = s.trim().split(':'),
                            name = arr && arr.length ? arr[0].trim() : '',
                            value = name && arr.length > 1 ? arr[1].trim() : name;
                        if (name) map[name] = value;
                    })
                }
                model.map = map;
                model.block = parent;

				let updateattrs = {};
                for(const p in map) {
                    const name = map[p];
                    if (p == 'content') {
                        setAttr(parent, name, model.get('content'));
                    } else {
                        setAttr(parent, name, getAttr(model, p));
                    }

                    model.listenTo(model.block, 'change:attributes:' + name, () => {
                        if (p == 'content') {
                            model.set('content', getAttr(parent, name));
                        } else {
                            setAttr(model, p, getAttr(parent, name));
                        }
                    })
                }
			}
			
			var listtype = getAttr(model, 't4-list');
			if (listtype) {
				if (!listtype.match(/:/)) {
					listtype = block.id + ':' + listtype;
					setAttr(model, 't4-list', listtype);
				}
				model.set('droppable', '[t4-type="' + listtype + '"]');
			}
			model.components().each(com => {
				if (!com.get('t4-type') && 
					!com.get('editable')) { 
					com.set({
						selectable: false,
						highlightable: false
					})
				}
				
				this.updateChild(com, block, listtype, _p);
			})
		},

	}	
}