import Backbone from 'backbone';
import { isUndefined, isString, isFunction } from 'underscore';
export default (editor, opt) => {
    editor.TraitManager.addType('animation', {

        createInput() {
            // custom option on setting default
            const options = editor.editor.get('t4-config').animation;
            const hasLabel = this.hasLabel && this.hasLabel();
            // Create a new element container and add some content
            const el = document.createElement('section');
            el.setAttribute('class', "custom-trait trait-anim");
            el.setAttribute('id', "trait-anim");
            const { ppfx, clsField } = this;
            el.innerHTML = `
	      <div class="u-m-bottom--s custom-trait-row grid grid--align-center grid--gutters-l">
	      	<div class="custom-trait-row__label grid-item grid-item--behavior-fixed"><span title="" data-tooltip="${this.getLabel()}">${this.getLabel()}</span></div>
	        <div class="custom-trait-row__field">
	        	<div class="input-group-container is-relative">
					<div class="is-rel">
        	            <select class="animation__type input-group__input input-group__input--select input-box">
        	              <option value="">None</option>
        	              ${
        	                Object.keys(options).map(opt => `<optgroup label="${opt}">${options[opt].map(opt2 => `<option value="${opt2}">${opt2}</option>`).join('')}</optgroup>`)
        	              }
        	            </select>
    		            <div class="${ppfx}sel-arrow">
    		                  <div class="${ppfx}d-s-arrow"></div>
    		              </div>
					</div>
	        	</div>
	        </div>
	      </div>

      `;
            this.animation_property = document.createElement('div');
            this.animation_property.setAttribute('class', "custom-trait-animation__property");
            this.animation_property.innerHTML = `
        <div class="u-m-bottom--s custom-trait-row grid grid--align-center grid--gutters-l">
            <div class="custom-trait-row__label grid-item grid-item--behavior-fixed"><span title="" data-tooltip="Delay (in ms)">Delay (in ms)</span></div>
            <div class="custom-trait-row__field">
                <div class="input-group-container is-relative">
                    <input type="number" name="delay" id="input-delay" placeholder="eg. 100" class="input-group__input input-group__input--number input-box">
                </div>
            </div>
        </div>
        <div class="u-m-bottom--s custom-trait-row grid grid--align-center grid--gutters-l">
            <div class="custom-trait-row__label grid-item grid-item--behavior-fixed"><span title="" data-tooltip="Duration (in ms)">Duration (in ms)</span></div>
            <div class="custom-trait-row__field">
                <div class="input-group-container is-relative">
                    <input type="number" name="duration" id="input-duration" placeholder="eg. 100" class="input-group__input input-group__input--number input-box">
                </div>
            </div>
        </div>
      `;

            // Let's make our content interactive
            const inputType = el.querySelector('.animation__type');
            inputType.addEventListener('change', ev => {
                if (ev.target.value) {
                    el.append(this.animation_property);
                } else {
                    if (el.childElementCount > 1) el.removeChild(this.animation_property);
                }
            });
            return el;
        },

        // Update the component based element changes
        onEvent({ elInput, component }) {
            // `elInput` is the result HTMLElement you get from `createInput`
            const inputType = elInput.querySelector('.animation__type');
            const anim_type = inputType.value;
            let active = component.view.$el.closest('active');

            if (anim_type) {
                const delayVal = elInput.querySelector('#input-delay').value;
                const durationVal = elInput.querySelector('#input-duration').value;
                //  animation-delay:1s;
                // animation-duration:2s;
                component.addStyle({'animation-name':anim_type});
                setTimeout(function() {
                   component.removeStyle('animation-name');
                }, 1000);
                delayVal && component.addStyle({ 'animation-delay': delayVal + "ms", 'transition-delay': delayVal + "ms"});

                if(!delayVal){
                    component.removeStyle('animation-delay');
                    component.removeStyle('transition-delay');
                }
                if(!durationVal){
                    component.removeStyle('animation-duration');
                    component.removeStyle('transition-duration');
                }
                durationVal && component.addStyle({ 'animation-duration': durationVal + "ms",'transition-duration': durationVal + "ms" });
                component.addAttributes({ 'data-anim-type': anim_type });

            } else {
            	const attr = component.getAttributes();
				delete attr['data-anim-type'];
				component.setAttributes(attr);
                component.removeStyle('animation-name');
                component.removeStyle('animation-delay');
                component.removeStyle('transition-delay');
                component.removeStyle('animation-duration');
                component.removeStyle('transition-duration');
            }
            if(active){
                component.view.$el.addClass('active');
            }
        },
        hasLabel() {
            return false;
        },
        onUpdate({ elInput, component }) {
            const anim_type = component.getAttributes()['data-anim-type'] || '';
            const inputType = elInput.querySelector('.animation__type');
            const inputDelay = this.animation_property.querySelector('#input-delay');
            const inputDuration = this.animation_property.querySelector('#input-duration');
            const styles = component.getStyle();
            if (inputDelay && styles.hasOwnProperty('animation-delay')) inputDelay.value = styles['animation-delay'].replace("ms", "");
            if (inputDuration && styles.hasOwnProperty('animation-duration')) inputDuration.value = styles['animation-duration'].replace("ms", "");
            inputType.value = anim_type;
            inputType.dispatchEvent(new CustomEvent('change'));
            inputDelay.dispatchEvent(new CustomEvent('change'));
            inputDuration.dispatchEvent(new CustomEvent('change'));
        },
        templateInput() {
            const { ppfx, clsField } = this;
            return `<div class="${clsField}" data-input></div>`;
        },
    });

}