export default (editor, config = {}) => {
    const em = editor.editor;
    const dc = editor.DomComponents;
    const defaultType = dc.getType('default');
    const defaultModel = defaultType.model;
    const defaultView = defaultType.view;
    const compType = 'editorxtd';

    dc.addType(compType, {

        model: defaultModel.extend({
            defaults: {
                ...defaultModel.prototype.defaults,

                name: 'Editorxtd',
                tagName: 'meta',
                void: true,
                droppable: false,
                stylable: false,
                selectable: false,
                traits: []
            },

            init() {
                if (this.opt.temporary) return;
                em.set('editorxtd', this);
            },

            toHTML() {return ''}
        }, {
            isComponent(el) {
                return false;
            }
        }),

        view: defaultView.extend({
            onRender() {
                const e = new CustomEvent('t4:xtd-init', {detail: this.model});
                document.dispatchEvent(e);                
            }
        }),
    });

    document.addEventListener('t4:xtd-insert', (e) => {
        const model = em.get('editorxtd'),
            coll = model.parent().components(),
            idx = coll.findIndex(model);
        coll.remove(model);
        coll.add(e.insertValue, {at: idx})
    })
}
