export default (editor, config = {}) => {

  const pn = editor.Panels;
  const vfull = 'view-full';
  const vhalf = 'view-half';
  const vcol = 'view-col';
  
  // Add devices buttons
  const panelDevices = pn.addPanel({id: 'devices-c'});
  //don't plic
  const viewDevices = pn.addPanel({id: 'devices-a'});
  viewDevices.get('buttons').add([{
    id: 'view-full',
    className: 'view-column view-full',
    command: vfull,
    label: `<svg width="20" height="20" xmlns="http://www.w3.org/2000/svg" xmlns:svg="http://www.w3.org/2000/svg">
 <!-- Created with SVG-edit - http://svg-edit.googlecode.com/ -->
 <g>
  <line id="svg_1" y2="17.74206" x2="16.86177" y1="2.65839" x1="16.99526" stroke="currentColor" fill="currentColor"/>
  <line id="svg_2" y2="17.7092" x2="17.87397" y1="2.62552" x1="18.00745" stroke="currentColor" fill="currentColor"/>
  <line id="svg_3" y2="17.72563" x2="3.58775" y1="2.64196" x1="3.72123" stroke="currentColor" fill="currentColor"/>
  <line id="svg_4" y2="17.69277" x2="2.59769" y1="2.60909" x1="2.73117" stroke="currentColor" fill="currentColor"/>
 </g>
</svg>`,
    active: 1,
    attributes: {
      title: '',
          'data-tooltip-pos': 'bottom',
          'data-tooltip': 'Full Width Viewport'
    }
  },{
    id: 'view-half',
    className: 'view-column view-half',
    command: vhalf,
    label: `<svg width="20" height="20" xmlns="http://www.w3.org/2000/svg" xmlns:svg="http://www.w3.org/2000/svg">
 <!-- Created with SVG-edit - http://svg-edit.googlecode.com/ -->
 <g>
  <line id="svg_1" y2="17.74206" x2="13.92513" y1="2.65839" x1="14.05861" stroke="currentColor" fill="currentColor"/>
  <line id="svg_2" y2="17.7092" x2="14.93732" y1="2.62552" x1="15.07081" stroke="currentColor" fill="currentColor"/>
  <line id="svg_3" y2="17.72563" x2="5.99046" y1="2.64196" x1="6.12394" stroke="currentColor" fill="currentColor"/>
  <line id="svg_4" y2="17.69277" x2="5.0004" y1="2.60909" x1="5.13388" stroke="currentColor" fill="currentColor"/>
 </g>
</svg>`,
    attributes: {
      title: '',
          'data-tooltip-pos': 'bottom',
          'data-tooltip': 'Half Width Viewport'
    }
  },{
    id: 'view-col',
    className: 'view-column view-col',
    command: vcol,
    label: `<svg width="20" height="20" xmlns="http://www.w3.org/2000/svg" xmlns:svg="http://www.w3.org/2000/svg">
 <!-- Created with SVG-edit - http://svg-edit.googlecode.com/ -->
 <g>
  <line id="svg_1" y2="17.74206" x2="12.32332" y1="2.65839" x1="12.45681" stroke="currentColor" fill="currentColor"/>
  <line id="svg_2" y2="17.7092" x2="13.33552" y1="2.62552" x1="13.469" stroke="currentColor" fill="currentColor"/>
  <line id="svg_3" y2="17.72563" x2="7.72575" y1="2.64196" x1="7.85923" stroke="currentColor" fill="currentColor"/>
  <line id="svg_4" y2="17.69277" x2="6.73569" y1="2.60909" x1="6.86917" stroke="currentColor" fill="currentColor"/>
 </g>
</svg>`,
    attributes: {
      title: '',
          'data-tooltip-pos': 'bottom',
          'data-tooltip': 'Column Viewport'
    }
  }]);

  var cmdDeviceDesktop = 'set-device-desktop',
      cmdDeviceTablet = 'set-device-tablet',
      cmdDeviceMobileLandscape = 'set-device-mobile-landscape',
      cmdDeviceMobile = 'set-device-mobile-portrait';
  panelDevices.get('buttons').add([{
        id: cmdDeviceDesktop,
        command: cmdDeviceDesktop,
        className: 'fal fa-desktop-alt',
        attributes:{
          title: '',
          'data-tooltip-pos': 'bottom',
          'data-tooltip': 'Desktop Layout'
        },
        active: true,
      },{
        id: cmdDeviceTablet,
        command: cmdDeviceTablet,
        className: 'fal fa-tablet-android',
        attributes:{
          title: '',
          'data-tooltip-pos': 'bottom',
          'data-tooltip': 'Tablet Layout'
        }
      },{
        id: cmdDeviceMobileLandscape,
        command: cmdDeviceMobileLandscape,
        className: 'fal fa-mobile-android mobile-landscape',
        attributes:{
          title: '',
          'data-tooltip-pos': 'bottom',
          'data-tooltip': 'Mobile Landscape Layout'
        }

      },{
        id: cmdDeviceMobile,
        command: cmdDeviceMobile,
        className: 'fal fa-mobile-android',
        attributes:{
          title: '',
        'data-tooltip-pos': 'bottom',
        'data-tooltip': 'Mobile Portrait Layout'
        }
    }]);
  let cm = editor.Commands;
  cm.add(cmdDeviceDesktop, {run: e => e.setDevice('Desktop'), stop(){},});
  cm.add(cmdDeviceTablet, {run: e => e.setDevice('Tablet'), stop(){}});
  cm.add(cmdDeviceMobile, {run: e => e.setDevice('Mobile portrait'), stop(){}});
  cm.add(cmdDeviceMobileLandscape, {run:  e => e.setDevice('Mobile landscape'), stop(){}});
  cm.add(vfull, e => e.Canvas.getBody().setAttribute('data-view', 'full'));
  cm.add(vhalf, e => e.Canvas.getBody().setAttribute('data-view', 'half'));
  cm.add(vcol, e => e.Canvas.getBody().setAttribute('data-view', 'col'));

  //set active default on root index file
  /*editor.on('load', model => {
    editor.Panels.getButton('devices-c', cmdDeviceDesktop).set('active',true);
  });*/
  
}