/**
 * You can customize the initial state of the module from the editor initialization, by passing the following [Configuration Object](https://github.com/artf/grapesjs/blob/master/src/device_manager/config/config.js)
 * ```js
 * const editor = grapesjs.init({
 *  deviceManager: {
 *    // options
 *  }
 * })
 * ```
 *
 * Once the editor is instantiated you can use its API. Before using these methods you should get the module from the instance
 *
 * ```js
 * const deviceManager = editor.DeviceManager;
 * ```
 *
 * * [add](#add)
 * * [get](#get)
 * * [getAll](#getAll)
 *
 * @module DeviceManager
 */

module.exports = () => {
    var c = {},
        defaults = require('./config/config'),
        Crumbs = require('./model/Crumbs'),
        crumbsView = require('./view/CrumbsView'),
        em;
    var crumbs, view;

    return {
        /**
         * Name of the module
         * @type {String}
         * @private
         */
        name: 'ButtonPanelManager',

        /**
         * Initialize module. Automatically called with a new instance of the editor
         * @param {Object} config Configurations
         * @param {Array<Object>} [config.styles=[]] Default styles
         * @example
         * ...
         * {
         *    styles: [
         *      {name: 'Desktop', width: ''}
         *      {name: 'Tablet', width: '991px'}
         *    ],
         * }
         * ...
         * @return {this}
         * @private
         */
        init(config) {
            c = config || {};
            em = c.em;
            c.crumbs = em.getComponents().models | [];
            for (var name in defaults) {
                if (!(name in c)) c[name] = defaults[name];
            }

            crumbs = new Crumbs(c.crumbs);
            view = new crumbsView({
                collection: crumbs,
                config: c
            });
            em.on('load', () => {
                em.get('Panels').getPanel('crumbs-view').set('appendContent', this.render());
            })
            em.on('component:selected', () => {

                let selector = em.getSelected();
                let checkModels = [];
                checkModels = em.get('selectModel') ? em.get('selectModel') : [];
                if (checkModels.length && checkModels.indexOf(selector) != '-1') {
                    em.set('modcid', selector.cid);
                    em.trigger('change:selector');
                    return;
                }
                let models = this.getParents(selector);
                if (models.length) em.set('selectModel', models);
                view.el.querySelector('#gjs-input-holder').innerHTML = '';
                view.el.querySelector('#gjs-input-holder').append(this.addEvents(models));
            });
            em.on('change:selector', this.changeActive);
            return this;
        },
        addEvents(models) {
            let crumbEl = document.createElement('div');
            models.forEach(mod => {
                let active = '';
                if (mod == models[models.length - 1]) {
                    em.set('modcid', mod.cid);
                    active = 'active';
                }
                let crumbItem = document.createElement('div');
                crumbItem.setAttribute('class', 'gjs-pn-crumbTab ' + mod.cid + ' ' + active);
                crumbItem.setAttribute('type', 'button');
                crumbItem.innerHTML = mod.getName();
                crumbItem.addEventListener('click', function() {
                    em.set('modcid', mod.cid);
                    em.trigger('change:selector');
                    em.setSelected(mod);
                });
                crumbEl.append(crumbItem);
            });
            return crumbEl;
        },
        getParents(selector) {
            let models = [];
            while (selector && selector.getName() !== '') {
                if (selector.attributes.selectable == true) {
                    models.push(selector);
                }

                selector = selector.parent();
            }
            models.reverse();
            return models;
        },
        /**
         * Return all styles
         * @return {Collection}
         * @example
         * var styles = deviceManager.getAll();
         * console.log(JSON.stringify(styles));
         * // [{name: 'Desktop', width: ''}, ...]
         */
        reset(crumbs) {
            view.collection.reset(crumbs);
            view.render();
        },

        /**
         * Render styles
         * @return {string} HTML string
         * @private
         */
        render() {
            return view.render().el;
        },
        changeActive() {
            view.el.querySelectorAll('.gjs-pn-crumbTab').forEach(crumb => crumb.classList.remove('active'));
            const name = em.get('modcid');
            if (view.el.querySelector('.' + name)) {
                view.el.querySelector('.' + name).classList.add('active');
            }
        }
    };
};