const allitems = {};
export default {
	extend: 't4.accordion',

	isComponent: el => {
		if (!el.className) return false;
		return el.classList.contains('t4b-tabs-container')
	},

	model: {
		defaults: {
			itemname: 'Tab Item',
			name: 'Tab',
			smgroup: 'accordion',
			copyable:false,
		},
		getList() {
			const items = {};
			const contents = this.find('.tab-content'),
				handler = this.find('[role="tablist"]');

			if (!handler.length || !contents.length) return items;
			const handlers = handler[0].find('[data-toggle="tab"]');

			contents[0].components().each((c, i) => {
				const item = {content: c, handler: handlers.length > i ? handlers[i] : null},
					id = c.getId();

				if (item.handler) {
					item.handler.addAttributes({'data-target': '#' + id});

					item.handler.set({
						draggable: false,
						selectable: false,
						'toolbar': []
					})
					item.handler.parent().set({
						selectable: true,
						draggable: false,
						name: this.get('itemname'),
						smgroup: 'text'
					})
				}



				items[id] = item;
			})

			return items;			
		},

		getContentCollection() {
			const tablist = this.find('[role="tablist"]');
			return tablist[0].components();
		},

		getContentItem(id) {
			return this.getItem(id).handler;
		},

		removeItem(model, coll, opt) {
			const handler = model.components().at(0),
				id = handler.getAttributes()['data-target'].substr(1),
				item = this.getItem(id),
				items = this.getItem();

			item.content.remove();

			delete items[id];

			// get last item
			const coms = this.find('.tab-content')[0].components();
			if (coms.length) {
				this.setActive(coms.at(0).getId())
			} else {
				this.remove();
			}
		},

		cloneItem(model, coll, opt) {
			// clone handler and put item into list
			const items = this.getItem(),
				handler = model.components().at(0),
				orgId = handler.getAttributes()['data-target'].substr(1),
				orgItem = this.getItem(orgId),
				item = {};

			// clone handler
			const cloned = orgItem.content.clone(),
				id = cloned.getId();
				cloned.removeClass('active');
			orgItem.content.collection.add(cloned, {at: opt.at});
			handler.removeClass('active');
			item.handler = handler;
			item.content = cloned;
			item.handler.addAttributes({'data-target': '#' + id});

			item.content.setId(id);

			// put to list
			items[id] = item;

			// make active
			this.setActive(id);
		},

		movedItem(model, coll, opt) {

			if (opt.temporary) return;
			const 
				to = opt.to,
				handler = model.components().at(0),
				id = handler.getAttributes()['data-target'].substr(1),
				item = this.getItem(id);
			// move content tab
			const content = item.content,
				content_coll = content.collection,
				silent = {silent: true};
			// remove current pos
			content_coll.remove(content);
			// add to new post
			content_coll.add(content, {at: to, ...silent});

			// update view
			this.view.render();
			// active
			this.setActive(id);
		}

	},

	view: {
		events: {
			'click [data-toggle="tab"]': 'toggle',
		},

		toggle(e) {
			const id = e.target.getAttribute('data-target').substr(1);
			this.model.setActive(id);
		},

	}
}