export function updateAssets(editor) {
	const $ =  editor.Canvas.getWindow() ? editor.Canvas.getWindow().jQuery : "";
	if (!$) return;	
	const em = editor.editor;

	let assets = em.get('t4-assets') || {groups: [], blocks: []};
	const BlockIds = editor.getWrapper().findType('t4.section').map(com => com.getAttr && com.getAttr('t4-bid'));
	const ElementIds = editor.getWrapper().findType('t4.element').map(com => com.get('bid') || (com.getAttr && com.getAttr('t4-bid')));
	let blockIds = [...new Set([...BlockIds, ...ElementIds])];
	let groups = ['t4'];
	const blocks = em.get('t4-config').blocks.filter(bl => blockIds.indexOf(bl.id) >= 0);
	blockIds.forEach(id => {
		if (!id) return;
		const group = id.split('_')[0];
		groups.indexOf(group) == -1 && groups.push(group);
	})

	// add new group assets
	groups.forEach(gr => {
		if (assets.groups.indexOf(gr) == -1) {
			assets.groups.push(gr);
			addAssets(editor, em.get('t4-config').assets[gr]);
		}
	});
	// add css & new group assets
	blockIds.forEach(id => {
		if (assets.blocks.indexOf(id) == -1) {
			const block = em.get('t4-config').blocks.find(bl => bl.id == id);
			assets.blocks.push(id);
			block && addAssets(editor, block && block.assets);
		}
	});

	// push new css
	addCss(editor, blocks);

	// remove group assets
	assets.groups = assets.groups.filter(gr => {
		if (groups.indexOf(gr) == -1) {
			removeAssets(editor, em.get('t4-config').assets[gr]);
			return false;
		}
		return true;
	});
	// remove block assets
	assets.blocks = assets.blocks.filter(id => {
		if (blockIds.indexOf(id) == -1) {
			const block = em.get('t4-config').blocks.find(bl => bl.id == id);
			removeAssets(editor, block && block.assets);
			return false;
		}
		return true;
	});

	em.set('t4-assets', assets);
}


const addAssets = (editor, assets) => {
	if (!assets) return;
	const edoc = editor.Canvas.getDocument();
	assets.css && assets.css.forEach(url => {
		var link = document.createElement('link');
		link.rel = 'stylesheet';
		link.href = url;
		edoc.head.appendChild(link);
	})

	assets.js && assets.js.forEach(url => {
		var script = document.createElement('script');
		script.type = 'text/javascript';
		script.src = url;
		edoc.head.appendChild(script);
	})
}


const removeAssets = (editor, assets = {}) => {
	if (!assets) return;
	const $ = editor.Canvas.getWindow().jQuery;
	assets.css && assets.css.forEach(url => $('link[href="' + url + '"]').remove())
	assets.js && assets.js.forEach(url => $('script[src="' + url + '"]').remove())	
}


const addCss = (editor, blocks) => {
	const $ = editor.Canvas.getWindow().jQuery;
	let $cssel = $('#t4-builder-css');
	if (!$cssel.length) {
		$cssel = $('<style>').attr('id', 't4-builder-css');
		$cssel.appendTo('head');
	}

	let css = '';
	blocks.forEach(block => {
		css += block.css ? block.css + '\n' : '';
	})
	$cssel.text(css);
}
